<template>
  <div id="adminpage">
    <AlertBadRealmType/>
    <PagePermissionDenied/>
    <div v-show="!storeState.badRealmType && !storeState.permissionDenied">
      <Spinner/>
      <NavbarPageTitle
          :bold-text="localStore.realmAsked"
          :text="storeState.dictionary.titlePageUserList"
          :no-change="noChangesHaveBeenDone"
          :save-button-attendance="localStore.writeAllowed"
          :save-function="launchSaveRequest"
          :refresh-function="launchAdminsListRequest"
      >
      </NavbarPageTitle>
      <br>
      <div v-show="!storeState.loading">
        <b-card :title="storeState.dictionary.titleCardPageAdmin">
          <b-card-text>
            <strong>{{ storeState.dictionary.role.admin }}</strong>: {{ storeState.dictionary.roleExplanationsCardPageAdmin.admin }}<br>
            <strong>{{ storeState.dictionary.role.editor }}</strong>: {{ storeState.dictionary.roleExplanationsCardPageAdmin.editor }}<br>
            <strong>{{ storeState.dictionary.role.viewer }}</strong>: {{ storeState.dictionary.roleExplanationsCardPageAdmin.viewer }}<br>
            <strong>{{ storeState.dictionary.role.none }}</strong>: {{ storeState.dictionary.roleExplanationsCardPageAdmin.none }}<br>
          </b-card-text>
        </b-card>
        <br>
          <b-button @click="showModal('creationModal')" variant="success" v-show="localStore.writeAllowed">{{ storeState.dictionary.createAdminForm.createButton }}</b-button>
        <br>
        <br>
        <b-table size="sm" :empty-text=storeState.dictionary.tableEmptyText show-empty striped hover :items="items" :fields="fields">
          <!--Checkbox Management-->
          <template v-slot:head(selectCheckbox)="">
            <b-form-checkbox v-show="localStore.writeAllowed" @change="headCheckboxHandler" v-model="headCheckbox" name="headCheckbox" ></b-form-checkbox>
          </template>
          <template v-slot:cell(selectCheckbox)="index">
            <b-form-checkbox
                v-show="localStore.writeAllowed"
                v-if="index.item.username!==storeState.authenticationInfo.username||localStore.realmAsked!==storeState.authenticationInfo.realm"
                v-model="localStore.selectedUsers"
                :value=index.item.username />
          </template>

          <!--Dropdown Management-->
          <template v-slot:cell(selectRole)="index">
            <div v-if="localStore.writeAllowed">
              <!--single dropdown to set role -->
              <DropdownAdminsListPage
                  :dropdown-disabled="!(index.item.username!==storeState.authenticationInfo.username||localStore.realmAsked!==storeState.authenticationInfo.realm)"
                  :dropdown-label="localStore.adminsList[index.item.username].role"
                  dropdown-variant="secondary"
                  :on-dropdown-item-click-function="setDropdownLabel"
                  :user-role="localStore.adminsList[index.item.username].role"
                  :username="index.item.username"
              />
            </div>
            <div v-else>
              <!-- do not allow user logged to set role -->
                 {{index.item.role}}
            </div>
          </template>
        </b-table>
        <br>

        <!-- Set Multiple Roles Dropdown on Page Bottom -->
        <b-navbar v-if="localStore.selectedUsers.length > 0 && localStore.writeAllowed" fixed="bottom" :sticky=true id="bottomnavbar">
          <b-navbar-nav class="ml-auto">
            <b-nav-form>
                <DropdownAdminsListPage
                    :dropdown-disabled="false"
                    :dropdown-label="storeState.dictionary.dropdownLabelSetRole.part1 + dropdownLabelBottom + storeState.dictionary.dropdownLabelSetRole.part2 + localStore.selectedUsers.length + storeState.dictionary.dropdownLabelSetRole.part3 "
                    dropdown-variant="primary"
                    :on-dropdown-item-click-function="setDropdownLabel"
                    :user-role="dropdownLabelBottom"
                    :username="null"
                />
              <b-button variant="danger" size="sm" @click="showModal('removingConfirmation')">{{ storeState.dictionary.buttonDeleteAdmin.part1 }}{{ localStore.selectedUsers.length }}{{ storeState.dictionary.buttonDeleteAdmin.part2 }}</b-button>
            </b-nav-form>
          </b-navbar-nav>
        </b-navbar>
      </div>
    </div>

    <!-- Creation Modal -->
    <b-modal
        id="creationModal"
        ref="creationModal"
        hide-footer
        :title="storeState.dictionary.titleAdminCreationModal"
        no-close-on-backdrop
        no-close-on-esc
        hide-header-close
    >
      <p style="font-size: small; color: red" v-show="localStore.creatingError">
        {{ storeState.dictionary.createAdminForm.errorMessage }}
      </p>
      <b-card>
        <p style="font-size: small; color:blue; margin: 0">{{ storeState.dictionary.createAdminForm.instructions }}</p>
        <b-form>
          <!--email-->
          <b-form-group id="input-group-1">
            <label>
              {{ storeState.dictionary.createAdminForm.labels.email }}
              <span style="color: red">*</span>
            </label>
            <b-form-input
                id="input-1"
                v-model="localStore.adminCreated.email"
                type="email"
                :placeholder="storeState.dictionary.createAdminForm.placeholder.email"
            ></b-form-input>
          </b-form-group>

          <!--username-->
          <b-form-group id="input-group-2">
            <label>
              {{ storeState.dictionary.createAdminForm.labels.username }}
              <span style="color: red">*</span>
            </label>
            <b-form-input
                id="input-2"
                v-model="localStore.adminCreated.username"
                :placeholder="storeState.dictionary.createAdminForm.placeholder.username"
                required
            ></b-form-input>
          </b-form-group>

          <!--firstname-->
          <b-form-group
              id="input-group-2"
              :label="storeState.dictionary.createAdminForm.labels.firstname"
              label-for="input-2">
            <b-form-input
                id="input-2"
                v-model="localStore.adminCreated.firstname"
                :placeholder="storeState.dictionary.createAdminForm.placeholder.firstname"
            ></b-form-input>
          </b-form-group>

          <!--lastname-->
          <b-form-group
              id="input-group-2"
              :label="storeState.dictionary.createAdminForm.labels.lastname"
              label-for="input-2">
            <b-form-input
                id="input-2"
                v-model="localStore.adminCreated.lastname"
                :placeholder="storeState.dictionary.createAdminForm.placeholder.lastname"
            ></b-form-input>
          </b-form-group>

          <!--roles-->
          <b-form-group id="input-group-3" >
            <label>
              {{ storeState.dictionary.createAdminForm.labels.role }}
              <span style="color: red">*</span>
            </label>
            <b-form-select
                id="input-3"
                v-model="localStore.adminCreated.role"
                :options="localStore.roles"
                required
            >
            </b-form-select>
          </b-form-group>

          <!--submit-->
          <b-container>
            <b-row align-h="end">
              <b-button @click="closeModal">{{ storeState.dictionary.createAdminForm.closeButton }}</b-button>
              <b-button @click="createAdminUser" variant="success">{{ storeState.dictionary.createAdminForm.createButton }}</b-button>
            </b-row>
          </b-container>
        </b-form>
      </b-card>
    </b-modal>

    <!--Creation success / sending email modal-->
    <b-modal
        ref="creationSuccess"
        :title="storeState.dictionary.createAdminSuccessModal.title"
        hide-footer
    >
      <p><strong>{{ localStore.adminCreated.username }}</strong>{{ storeState.dictionary.createAdminSuccessModal.part1 }}
      </p>
      <p>
        {{ storeState.dictionary.createAdminSuccessModal.part2 }}
      </p>
      <p style="text-align: center; font-size: 200%; font-family: 'Courier', monospace;">
        <strong>
          {{ localStore.password }}
        </strong>
      </p>
      <p>
        {{ storeState.dictionary.createAdminSuccessModal.part3 }}{{ localStore.adminCreated.email }}{{ storeState.dictionary.createAdminSuccessModal.part4 }}
      </p>
      <b-container>
        <b-row align-h="end">
          <!--Sending success-->
          <p v-show="localStore.successfulSending === true" style="font-size: small; color: green">
            {{ storeState.dictionary.createAdminSuccessModal.successMessage }}
          </p>
          <!--Sending fail-->
          <p v-show="localStore.successfulSending === false"  style="font-size: small; color: red">
            {{ storeState.dictionary.createAdminSuccessModal.failMessage }}
          </p>
          <!--Sending button-->
          <b-button @click="sendPasswordEmail" variant="primary" v-show="localStore.successfulSending === null && localStore.smtpError === false">
            {{ storeState.dictionary.createAdminSuccessModal.emailButton }}
          </b-button>
          <!--Sending impossible / smtp not config-->
          <p v-show="localStore.successfulSending === null && localStore.smtpError === true" style="color: red; font-size: small">
            <span v-if="storeState.authenticationInfo.realm !== 'master'">{{ storeState.dictionary.createAdminSuccessModal.noSmtpConfig }}</span>
            <span v-else v-html="storeState.dictionary.createAdminSuccessModal.noSmtpConfigMaster "></span>
          </p>
          <!--close modals button-->
          <b-button @click="closeModal" variant="danger">
            {{ storeState.dictionary.createAdminSuccessModal.closeButton }}
          </b-button>
        </b-row>
      </b-container>
    </b-modal>

    <!--Removing Confirmation-->
    <b-modal ref="removingConfirmation" :title="storeState.dictionary.removingConfirmation.title" hide-footer>
      <p>{{ storeState.dictionary.removingConfirmation.question }}</p>
      <b-container>
        <b-row align-h="end">
          <b-button @click="closeModal">{{ storeState.dictionary.removingConfirmation.buttonCancelLabel }}</b-button>
          <b-button variant="primary" @click="removeAdmins">{{ storeState.dictionary.removingConfirmation.buttonOkLabel }}</b-button>
        </b-row>
      </b-container>
    </b-modal>
  </div>
</template>

<script>
import {store} from "@/main";
import {dictionary} from "@/dictionary/dictionary";
import {api} from "@/API/API";
import NavbarPageTitle from "./NavbarPageTitle";
import AlertBadRealmType from "./AlertBadRealmType";
import Spinner from "./Spinner";
import DropdownAdminsListPage from "./DropdownAdminsListPage";
import PagePermissionDenied from "@/components/PagePermissionDenied";
import Vue from "vue";
const localStore = {
  selectedUsers: [], //to know which user's role will be set
  adminsList: {},
  writeAllowed: false,
  realmAsked: "",
  roles: [
    {value: "", text: dictionary.createAdminForm.roles.defaultChoice},
    dictionary.createAdminForm.roles.admin,
    dictionary.createAdminForm.roles.editor,
    dictionary.createAdminForm.roles.viewer,
    dictionary.createAdminForm.roles.none,
  ],
  adminCreated: {
    email: "",
    username: "",
    firstname: "",
    lastname: "",
    role: "",
  },
  password: "",
  successfulSending: null,
  creatingError: false,
  smtpError: false,
}
export default {
  name: "PageAdminList",
  components: {PagePermissionDenied, DropdownAdminsListPage, Spinner, AlertBadRealmType, NavbarPageTitle},
  data() {
    return {
      storeState: store.state,
      localStore: localStore,
      items: [], //data displayed in table
      noChangesHaveBeenDone: true, //to disable/enable save button
      originalAdminsList: {}, //to disable/enable save button
      dropdownLabelBottom: store.state.dictionary.roleString,
      headCheckbox: false,
      fields: [
        {
          key: 'firstname',
          label: store.state.dictionary.fieldTable.firstname,
          thClass: 'align-middle',
        },
        {
          key: 'lastname',
          label: store.state.dictionary.fieldTable.lastname,
          thClass: 'align-middle',
        },
        {
          key: 'username',
          label: store.state.dictionary.fieldTable.username,
          thClass: 'align-middle',
        },
        {
          key: 'email',
          label: store.state.dictionary.fieldTable.email,
          thClass: 'align-middle',
        },
        {
          key: 'selectRole',
          label: store.state.dictionary.fieldTable.role,
          thStyle: {width: '125px'},
          thClass: 'align-middle',
        },
      ],
    }
  },
  methods: {

    isThereAnyChanges: function () {
      //to disable/enable save button
      this.noChangesHaveBeenDone = JSON.stringify(localStore.adminsList) === JSON.stringify(this.originalAdminsList)
      store.state.dontLeave = !this.noChangesHaveBeenDone
    },

    headCheckboxHandler: function () {
      //when the checkbox in head is checked, checked all the checkbox on its column, else, unselect them all
      localStore.selectedUsers = []
      if (this.headCheckbox) {
        for (let i = 0; i < this.items.length; i++) {
          //do not allow user logged to change its own role
          if (this.items[i].username !== store.state.authenticationInfo.username || this.storeState.realmAsked !== this.storeState.authenticationInfo.realm)
            localStore.selectedUsers.push(this.items[i].username)
        }
      }
    },

    makeToast(variant = null, delay, text, title) {
      this.$bvToast.toast(text, {
        title: title,
        variant: variant,
        toaster: 'b-toaster-top-center',
        autoHideDelay: delay,
        solid: true
      })
    },

    setDropdownLabel: function (username, role) {
      if (username != null) {
        //change one user's role
        this.$set(localStore.adminsList[username], "role", role)
      } else {
        //change multiple user's role in the same time
        this.dropdownLabelBottom = role
        for (let i = 0; i < localStore.selectedUsers.length; i++) {
          this.$set(localStore.adminsList[localStore.selectedUsers[i]], "role", role)
        }
      }
      //this.$forceUpdate() //if we want that dropdown text changes
      this.isThereAnyChanges() //to enable/disable save button
    },

    initializationTable: function () {
      localStore.selectedUsers = []
      this.headCheckbox = false
      if (localStore.writeAllowed) {
        this.fields.unshift(
            {
              key: 'selectCheckbox',
              label: '',
              thStyle: {width: '30px'},
              thClass: 'align-middle',
            },)
      }
      this.items = Object.values(localStore.adminsList)
      this.originalAdminsList = JSON.parse(JSON.stringify(localStore.adminsList))
      this.isThereAnyChanges()
    },

    manageRequestResult: function (result) {
      if (typeof result !== 'undefined') {
        localStore.adminsList = result.adminUsersList
        localStore.writeAllowed = result.writeAllowed
        this.initializationTable()
      }
    },

    launchSaveRequest: function () {
      store.state.loading = true
      api.setAdminsRoleRequest(localStore.realmAsked, localStore.adminsList)
          //success
          .then(
              (result) => {
                this.manageRequestResult(result)

                //end of the request
                this.makeToast('success', 4000, store.state.dictionary.toastSaveSuccess.text, store.state.dictionary.toastSaveSuccess.title)
                store.state.loading = false
              }
          )
          //fail
          .catch((error) => {
                console.log(error)
                this.makeToast("danger", 4000, store.state.dictionary.toastDanger.text, store.state.dictionary.toastDanger.title);
                store.state.loading = false
              }
          )
    },

    launchAdminsListRequest: function () {
      store.state.loading = true
      api.getAdminsListRequest(localStore.realmAsked)
          .then(
              (result) => {
                this.manageRequestResult(result)
                store.state.loading = false
              }
          )
          .catch((error) => {
                console.log(error)
                store.state.loading = false
              }
          )
    },

    createAdminUser: function () {
      const adminUser = {
        email: localStore.adminCreated.email,
        username: localStore.adminCreated.username,
        firstname: localStore.adminCreated.firstname,
        lastname: localStore.adminCreated.lastname,
        role: localStore.adminCreated.role,
      }
      localStore.creatingError = false
      localStore.successfulSending = null
      api.createAdminUser(JSON.stringify(adminUser))
          .then((result) => {
            localStore.creatingError = typeof result.error !== undefined && result.error === 3
            localStore.password = result.password
            localStore.smtpError = result.smtpError
            if (!localStore.creatingError) {
              Vue.set(localStore.adminsList, adminUser.username, adminUser)
              this.initializationTable()
              this.$refs['creationModal'].hide()
              this.showModal('creationSuccess')
            }
          })
          .catch((error) => {
            console.log(error)
          })
    },

    removeAdmins: function () {
      store.state.loading = true
      api.removeAdmins(JSON.stringify(localStore.selectedUsers))
          .then((result) => {
            store.state.loading = false
            this.manageRequestResult(result)
          })
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
      this.closeModal()
    },

    showModal: function (modalName) {
      this.$refs[modalName].show()
    },

    sendPasswordEmail: function () {
      store.state.loading = true
      api.sendPasswordEmail(localStore.password, localStore.adminCreated.username)
          .then((result) => {
            store.state.loading = false
            localStore.successfulSending = !(typeof result !== undefined && result.error === 1)
          })
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },

    closeModal: function () {
      this.$refs['creationModal'].hide()
      this.$refs['creationSuccess'].hide()
      this.$refs['removingConfirmation'].hide()
      localStore.creatingError = false
      localStore.adminCreated = {
        email: "",
        username: "",
        firstname: "",
        lastname: "",
        role: "",
      }
    }
  },
  beforeMount() {
    localStore.realmAsked = this.$route.params.realmName
    this.launchAdminsListRequest()
  },
}
</script>

<style scoped>
.modal-dialog .btn {
  margin-left: 20px;
}
</style>
