import Vue from "vue";
import VueRouter from "vue-router";
import {store} from "@/main";
import PageEventLogs from "../components/PageEventLogs";
import PageRealmsConfiguration from "../components/PageRealmsConfiguration";
import PageWelcome from "../components/PageWelcome";
import PageGroups from "../components/PageGroups";
import PageSecuritySettings from "../components/PageSecuritySettings";
import PageRevocationLogs from "../components/PageRevocationLogs";
import PageUsersList from "../components/PageUsersList";
import PageOlvidDashboard from "../components/PageOlvidDashboard";
import PageAdminsList from "../components/PageAdminsList";

Vue.config.productionTip = false;
Vue.use(VueRouter)

const router = new VueRouter({
    mode: "hash",
})

router.addRoutes([
    {
        path: '/:realmName/admin',
        name: ':realmName',
        component: PageAdminsList,
    },
    {
        path: '/:realmName/dashboard',
        name: ':realmName',
        component: PageOlvidDashboard,
    },
    {
        path: '/:realmName/users',
        name: ':realmName',
        component: PageUsersList,
    },
    {
        path: '/:realmName/revocation',
        name: ':realmName',
        component: PageRevocationLogs,
    },
    {
        path: '/:realmName/settings',
        name: ':realmName',
        component: PageSecuritySettings,
    },
    {
        path: '/:realmName/groups',
        name: ':realmName',
        component: PageGroups,
    },
    {
        path: '/',
        name: 'root',
        component: PageWelcome,
    },
    {
        path: '/realmsConfiguration',
        component: PageRealmsConfiguration,
    },
    {
        path: '/eventLogs',
        component:PageEventLogs,
    }
])

router.beforeEach((to, from, next) => {
    if ((from.path === "/realmsConfiguration" && store.state.dontLeave) || (from.path.match(/^\/.*\/admin$/) && store.state.dontLeave)) {
        if(confirm("Do you really want to leave?")){
            next()
        } else {
            next(false)
        }
    } else {
        next();
    }
});

export default router;