<template>
  <div id="eventLogs">
    <AlertBadRealmType/>
    <PagePermissionDenied/>
    <b-container v-show="!storeState.badRealmType && !storeState.permissionDenied">
      <spinner/>
      <NavbarPageTitle
          :bold-text="storeState.dictionary.titlePageEventLogs"
          :refresh-function="relaunchGetLogsRequest"
      />
      <br>
      <br>
        <b-form-datepicker v-model="selectedDate" class="mb-2"></b-form-datepicker>
      <br>
      <b-table small :fields="logFields" :items="localStore.logItems" responsive="lg">
        <template #cell(timestamp)="data">
          <span class="timestampCells">{{ data.item.timestamp }}</span>
        </template>
        <template #head(parameter)="data">
          <div class="parameterHead">{{ data.label }}</div>
        </template>
        <template #cell(parameter)="data">
          <span :ref="'parameter'+data.item.id" @click="setParameterClass('parameter'+data.item.id)" :id="'parameter'+data.item.id" class="parameterCells">{{ data.item.parameter }}</span>
        </template>
      </b-table>
    </b-container>
  </div>
</template>

<script>
import {store} from "@/main";
import {api} from "@/API/API";
import Spinner from "@/components/Spinner";
import NavbarPageTitle from "@/components/NavbarPageTitle";
import PagePermissionDenied from "@/components/PagePermissionDenied";
import AlertBadRealmType from "@/components/AlertBadRealmType";

const localStore= {
    logItems: [],
    fromTimestamp: 0,
    toTimestamp: 0,
    parameterCells: "",
    toggle:false,
}

export default {
  name: "PageEventLogs",
  components: {Spinner, NavbarPageTitle, PagePermissionDenied, AlertBadRealmType},
  data: function () {
    return {
      logFields: [
        {key: 'adminUsername', label: store.state.dictionary.logTableHeader.adminUsername},
        {key: 'timestamp', label: store.state.dictionary.logTableHeader.timestamp},
        {key: 'logType', label: store.state.dictionary.logTableHeader.logType},
        {key: 'parameter', label: store.state.dictionary.logTableHeader.parameter},
        {key: 'ipSource', label: store.state.dictionary.logTableHeader.ipSource},
      ],
      localStore: localStore,
      selectedDate: "",
      storeState: store.state,
    }
  },
  methods: {
    setParameterClass(id) {
      this.$refs[id].classList.toggle("parameterCells")
      this.$refs[id].classList.toggle("styleCells")
    },

    launchGetLogsRequest: function (fromTimestamp, toTimestamp) {
      store.state.loading = true
      api.getLogsRequest(fromTimestamp, toTimestamp).then(
          result => {
            if (typeof result.queryNotAllowed === undefined || !result.queryNotAllowed)
            this.manageRequestResult(result)
            store.state.loading = false
          }
      ).catch((error) => {
        console.log(error)
        store.state.loading = false
      })
    },

    manageRequestResult: function (result) {
      this.localStore.logItems = result.consoleLogs

      for (let i = 0; i < this.localStore.logItems.length; i++) {
        //timestamp to date
        const date = new Date(this.localStore.logItems[i].timestamp)
        this.localStore.logItems[i].timestamp = date.toLocaleString()
      }
    },
  },

  watch: {
    selectedDate: function() {
      const splitString = this.selectedDate.split("-")
      const date = new Date(Number(splitString[0]), Number(splitString[1]) - 1, Number(splitString[2])) //months numerated from 0 to 11
      date.setHours(0)
      date.setMinutes(0)
      date.setSeconds(0)
      date.setMilliseconds(0)
      this.localStore.fromTimestamp = date.getTime()
      date.setTime(date.getTime() + 36 * 60 * 60 * 1000)
      date.setHours(0)
      date.setMinutes(0)
      date.setSeconds(0)
      date.setMilliseconds(0)
      this.localStore.toTimestamp = date.getTime()

      this.launchGetLogsRequest(this.localStore.fromTimestamp, this.localStore.toTimestamp)
    }
  },

  beforeMount() {
    const date = new Date()
    this.selectedDate = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()
    date.setHours(0)
    date.setMinutes(0)
    date.setSeconds(0)
    date.setMilliseconds(0)
    this.localStore.fromTimestamp = date.getTime()
    date.setTime(date.getTime() + 36 * 60 * 60 * 1000)
    date.setHours(0)
    date.setMinutes(0)
    date.setSeconds(0)
    date.setMilliseconds(0)
    this.localStore.toTimestamp = date.getTime()

    this.launchGetLogsRequest(this.localStore.fromTimestamp, this.localStore.toTimestamp)
  },
}
</script>

<style scoped>
.timestampCells {
  white-space: nowrap;
  font-size: smaller;
}

.parameterHead {
  width: 500px;
}
.parameterCells{
  margin: 0;
  font-family: "Courier", monospace;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 500px;
  font-size: smaller;
  cursor: zoom-in;
}
.styleCells {
  margin: 0;
  font-family: "Courier", monospace;
  font-size: smaller;
  display: block;
  white-space: pre;
  width: 500px;
  overflow-x: auto;
  cursor: zoom-out;
}

</style>