<template>
  <div :id=" !storeState.permissionDenied ? 'userlist' : ''">

    <AlertBadRealmType/>
    <PagePermissionDenied/>

    <div v-show="!storeState.badRealmType && !storeState.permissionDenied">
      <NavbarPageTitle
          :bold-text="localStore.realmAsked"
          :text="storeState.dictionary.titlePageUserList"
          :save-button-attendance="false"
          :refresh-function="launchGetUsersListRequest"
      >
      </NavbarPageTitle><br><br>

      <Spinner/>

      <!-- modal management for revocation-->
      <b-modal id="revocationModal" ref="revocationModal" hide-footer :title="storeState.dictionary.revoke.revoke + localStore.textToDisplay" >
        <p class="my-4">
          <b-card no-body>
            <b-tabs pills card >
              <!-- revoke olvid id tab -->
              <b-tab
                  :disabled=revokeIdentityDisabled
                  @click="setRevocationType(storeState.dictionary.revoke.revokeIdentityKey)"
                  :title="storeState.dictionary.revoke.revokeIdentityLabel"
                  active
              >
                <b-card-text style="text-align: justify">
                  {{ storeState.dictionary.revoke.revokeIdentityText.part1 }}
                  <strong>{{ storeState.dictionary.revoke.revokeIdentityText.part2 }}</strong>
                  {{ storeState.dictionary.revoke.revokeIdentityText.part3 }}
                </b-card-text>
              </b-tab>

              <!-- remove user tab -->
              <b-tab
                  @click="setRevocationType(storeState.dictionary.revoke.removeUserKey)"
                  :title="storeState.dictionary.revoke.removeUserLabel">
                <b-card-text style="text-align: justify">
                  {{ storeState.dictionary.revoke.removeUserText.part1 }}
                  <strong>{{ storeState.dictionary.revoke.removeUserText.part2 }}</strong>
                  {{ storeState.dictionary.revoke.removeUserText.part3 }}
                </b-card-text>
              </b-tab>

            </b-tabs>
          </b-card>
          <span style="font-size: 13px;" v-show="revokeIdentityDisabled"><strong style="margin-right: 5px">{{ storeState.dictionary.revoke.note.part1 }}</strong>{{ storeState.dictionary.revoke.note.part2 }}</span>
          <br>
        </p>
            <b-btn  size="sm" style="float: right; margin-left: 4px;" variant="danger" @click="hideModal('revocationModal',false)" v-b-modal.areYouSureModal>
              <span v-if="revocationType===storeState.dictionary.revoke.revokeIdentityKey">{{ storeState.dictionary.revoke.revokeIdentityLabel }}</span>
              <span v-else-if="revocationType===storeState.dictionary.revoke.removeUserKey">{{ storeState.dictionary.revoke.removeUserLabel }}</span>

            </b-btn>
            <b-btn  size="sm" style="float: right" variant="secondary" @click="hideModal('revocationModal',true)" > Cancel </b-btn>
      </b-modal>

      <!-- second modal to confirm revocation -->
      <b-modal
          id="areYouSureModal"
          ref="areYouSureModal"
          :title="storeState.dictionary.titleModalAreYouSure"
          @cancel="showModal('revocationModal')"
          @ok="launchRevocationRequest()"
          :ok-title="storeState.dictionary.revoke.revoke"
          button-size="sm"
      >
        <p class="my-4">
          {{ storeState.dictionary.revoke.modalText.part1 }}{{localStore.textToDisplay }}{{ storeState.dictionary.revoke.modalText.part2 }}
          <strong v-if="revocationType===storeState.dictionary.revoke.revokeIdentityKey">'{{ storeState.dictionary.revoke.revokeIdentityLabel }}'</strong>
          <strong v-else-if="revocationType===storeState.dictionary.revoke.removeUserKey">'{{ storeState.dictionary.revoke.removeUserLabel }}'</strong>

          {{ storeState.dictionary.revoke.modalText.part3 }}
        </p>
      </b-modal>


      <!-- radio buttons that allow displaying according to the existence of OlvidID -->
      <b-container>
        <b-row cols="2" cols-sm="12" cols-md="12" cols-lg="12" cols-xl="12">
          <b-col cols="4">
          <div>
            <b-form-group size="sm" v-slot="{ ariaDescribedby }">
              <b-form-radio-group
                  v-model="radioSelected"
                  :options="radioOptions"
                  :aria-describedby="ariaDescribedby"
                  name="radio-inline"
                  @change="displayTheRightData"
                  stacked
              ></b-form-radio-group>
            </b-form-group>
          </div>
          </b-col>
          <b-col cols="8">
            <!--  Filter input  -->
            <b-form-group
                :label="storeState.dictionary.labelFilter"
                label-for="filter-input"
                label-cols-sm="3"
                label-align-sm="right"
                label-size="sm"
                class="mb-0"
            >

              <b-input-group size="sm">
                <b-form-input
                    id="filter-input"
                    v-model="filter"
                    type="search"
                    :placeholder="storeState.dictionary.placeholderUserListFilter"
                    @keyup.enter="onSearchButton"
                ></b-form-input>
                <b-input-group-append>
                  <b-button :disabled="!filter" @click="onSearchButton">{{ storeState.dictionary.filterButtonLabel.search }}</b-button>
                  <b-button :disabled="filters.length===0" @click="onClearButton">{{ storeState.dictionary.filterButtonLabel.clear }}</b-button>
                </b-input-group-append>
              </b-input-group>
              <b-form-tag
                  v-for="tag in filters"
                  @remove="removeTag(tag)"
                  :key="tag"
                  :title="tag"
                  class="mr-1"
              >{{ tag }}</b-form-tag>
            </b-form-group>
          </b-col>
        </b-row>
      </b-container><br>

        <!-- table displaying, according radioButton's value -->
        <b-table
            :items="items"
            :fields="fields"
            :filter="filter"
            :filter-function="onFilter"
            :filter-included-fields="columnsToFilter"
            filter-debounce="250"
            :empty-text='storeState.dictionary.tableEmptyText'
            style="text-align:center;"
            striped hover
            show-empty
            responsive
        >

          <!-- checkbox management -->
          <template v-slot:head(selectUsers)="">
            <b-form-checkbox v-show="localStore.writeAllowed" id="headcheckbox" @change="headCheckboxHandler" v-model="headCheckbox" name="headcheckbox" />
          </template>
          <template v-slot:cell(selectUsers)="index">
            <b-form-checkbox v-show="localStore.writeAllowed" class="selectionCheckbox" v-model="localStore.selectedUsers" :value=index.item.id />
          </template>

          <!-- tooltip displaying id-->
          <template v-slot:cell(username)="data">
            <span v-b-tooltip.hover :title=data.item.id v-b-tooltip.left.v-light>{{ data.value }}</span>
          </template>

          <template v-slot:cell(apiKey)="data">
            <span class="apikey">{{ data.value }}</span>
          </template>

          <!-- signout management -->
          <template v-slot:head(signOut)="index">
            <span v-show="localStore.writeAllowed"> {{ index.label }} </span>
          </template>
          <template v-slot:cell(signOut)="index">
            <b-button v-show="localStore.writeAllowed" :name="index.item.id" :id="index.item.id" @click="launchSignOutRequest(index.item.id)"  size="sm" variant="outline-secondary">{{ storeState.dictionary.revoke.signOutLabel}}</b-button>
          </template>

          <!-- revocation management -->
          <template v-slot:head(isrevoked)="index">
            <span v-show="localStore.writeAllowed">{{ index.label }}</span>
          </template>
          <template v-slot:cell(isrevoked)="index">
            <b-button v-show="localStore.writeAllowed" :name="index.item.id" :id="index.item.id" @click="singleRevocation(index.item.id,index.item.username,index.item.invitationLink)"  size="sm" variant="outline-danger">{{ storeState.dictionary.revoke.revoke}}</b-button>
          </template>

          <!-- invitation link management -->
          <template v-slot:cell(invitationLink)="index">
            <a v-if="typeof index.item.invitationLink !== 'undefined' && (typeof index.item.firstname!=='undefined' || typeof index.item.lastname!=='undefined') " :href="index.item.invitationLink" target="_blank">
              <font-awesome-icon icon="link" style="font-size: 20px" />
            </a>
            <!--<font-awesome-icon icon="copy" style="font-size: 20px; margin-left: 10px; color: grey" />-->
          </template>
        </b-table>

        <br>

      <!--  Display the page selector  -->
      <div class="mt-3">
        <b-pagination
            v-model="currentPage"
            :total-rows="localStore.count"
            :per-page="storeState.limit"
            first-number
            last-number
            @change="onPageChanged"
        ></b-pagination>
      </div>

      <!--  Revocation  -->
      <b-navbar v-show="localStore.writeAllowed && localStore.selectedUsers.length > 0" fixed="bottom" :sticky=true id="bottomnavbar">
        <b-navbar-nav class="ml-auto">
          <b-nav-form>
            <div class="buttons" v-show="localStore.selectedUsers.length>0">
              <!-- Revocation Button -->
              <span>
                <b-button class="revocationButton"  size="sm" variant="outline-danger"  @click="multipleRevocations">
                  {{ storeState.dictionary.revoke.revoke}}
                  <span v-if="localStore.selectedUsers.length===1">1 {{ storeState.dictionary.user}}</span>
                  <span v-else> {{ localStore.selectedUsers.length }} {{storeState.dictionary.users }}</span>
                </b-button>
                <b-button class="revocationButton"  size="sm" variant="outline-secondary"  @click="launchSignOutRequest(null)">
                  {{ storeState.dictionary.revoke.signOutLabel }}
                  <span v-if="localStore.selectedUsers.length===1">1 {{ storeState.dictionary.user }}</span>
                  <span v-else> {{ localStore.selectedUsers.length }} {{storeState.dictionary.users }}</span>
                </b-button>
              </span>
            </div>
          </b-nav-form>
        </b-navbar-nav>
      </b-navbar>
    </div>
  </div>

</template>

<script>
import {store} from "@/main";
import {api} from "@/API/API";
import NavbarPageTitle from "./NavbarPageTitle";
import AlertBadRealmType from "./AlertBadRealmType";
import PagePermissionDenied from "@/components/PagePermissionDenied";
import Spinner from "./Spinner";

const localStore = {
  selectedUsers:[],
  users:{},
  limit:100,
  count:0,
  textToDisplay:"",
  writeAllowed:false,
  showOnlyOneCardTab:false,
  realmAsked: "",
}

export default {
  name: "PageUsersList",
  components: {Spinner, AlertBadRealmType, NavbarPageTitle, PagePermissionDenied},
  data() {
    return {
      storeState: store.state,
      localStore: localStore,
      revocationType: '', // which kind of revocation will use the user
      toRevoke: [], //to know which user(s) we have to revoke, when revoke's button has been clicked
      revokeIdentityDisabled: false,
      items: [], //dataDisplayed
      filter: '',
      filters: [],
      headCheckbox: false,
      currentPage: 1,
      radioSelected: "withOlvidId",
      radioOptions: [
        {text: store.state.dictionary.radioOptionsUserList.withOlvidId, value: 'withOlvidId'},
        {text: store.state.dictionary.radioOptionsUserList.withoutOlvidId, value: 'withoutOlvidId'},
        {text: store.state.dictionary.radioOptionsUserList.both, value: 'both'}
      ],
      fields: [
        {
          key: 'username',
          label: store.state.dictionary.fieldTable.username,
          sortable: true,
          thClass: 'align-middle',
        },
        {
          key: 'firstname',
          label: store.state.dictionary.fieldTable.firstname,
          sortable: true,
          thClass: 'align-middle',
        },
        {
          key: 'lastname',
          label: store.state.dictionary.fieldTable.lastname,
          sortable: true,
          thClass: 'align-middle',
        },
        {
          key: 'position',
          label: store.state.dictionary.fieldTable.position,
          sortable: true,
          thClass: 'align-middle',
        },
        {
          key: 'company',
          label: store.state.dictionary.fieldTable.company,
          sortable: true,
          thClass: 'align-middle',
        },
        {
          key: 'apiKey',
          label: store.state.dictionary.fieldTable.apiKey,
          sortable: true,
          thClass: 'align-middle',
        },
        {
          key: 'invitationLink',
          label: store.state.dictionary.fieldTable.invitationLink,
          thStyle: {width: '65px'},
          thClass: 'align-middle',
        },
      ],
      columnsToFilter: ["username", "firstname", "lastname", "position", "company"]
    }
  },
  methods: {

    //this two methods are used for the management of the modal
    showModal(modal) {
      this.$refs[modal].show()
    },

    hideModal(modal, cancelButtonClicked) {
      this.$refs[modal].hide()
      if (cancelButtonClicked) {
        this.revocationType = store.state.dictionary.revoke.revokeIdentityKey
      }
    },

    headCheckboxHandler: function () {
      //when the checkbox in head is checked, checked all the checkbox on its column, else, unselect them all
      localStore.selectedUsers = []
      if (this.headCheckbox) {
        console.log("== headCheckboxHandler ==")
        for (let i = 0; i < this.items.length; i++) {
          console.log("filter : ", this.filter)
          console.log("item[i] : ", this.items[i])
          console.log("condition : ", this.onFilter(this.items[i], this.filter))
          if (this.onFilter(this.items[i], this.filter)) {
            localStore.selectedUsers.push(this.items[i].id)
          }
        }
      console.log("selectedUsers : ", localStore.selectedUsers)
      }
    },

    onSearchButton: function () {
      store.state.loading = true
      this.filters = this.filter !== "" ? this.filters.concat(this.filter.split(" ")) : this.filters
      this.filters = this.filters.filter(e => e);
      api.getUsersListRequest(localStore.realmAsked, this.filters, 1)
          .then(
              (result) => {
                this.localStore.users = result.userList
                this.localStore.count = result.count
                this.localStore.writeAllowed = result.writeAllowed
                store.state.loading = false
                this.displayTheRightData()
                this.filter = ""
                store.state.loading = false
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },

    removeTag(tag) {
      store.state.loading = true
      const index = this.filters.indexOf(tag);
      if (index > -1) {
        this.filters.splice(index, 1);
      }
      api.getUsersListRequest(localStore.realmAsked, this.filters, 1)
          .then(
              (result) => {
                this.localStore.users = result.userList
                this.localStore.count = result.count
                this.localStore.writeAllowed = result.writeAllowed
                store.state.loading = false
                this.displayTheRightData()
                this.filter = ""
                store.state.loading = false
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },

    onClearButton: function () {
      store.state.loading = true
      this.filters = []
      this.filter = ""
      api.getUsersListRequest(localStore.realmAsked, this.filters, 1)
          .then(
              (result) => {
                this.localStore.users = result.userList
                this.localStore.count = result.count
                this.localStore.writeAllowed = result.writeAllowed
                store.state.loading = false
                this.displayTheRightData()
                this.filter = ""
                store.state.loading = false
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },

    onPageChanged: function (page) {
      store.state.loading = true
      api.getUsersListRequest(localStore.realmAsked, this.filters, page)
          .then(
              (result) => {
                this.localStore.users = result.userList
                this.localStore.count = result.count
                this.localStore.writeAllowed = result.writeAllowed
                store.state.loading = false
                this.displayTheRightData()
                this.filter = ""
                store.state.loading = false
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },

    onFilter: function (row, filter) {
      const filters = filter.split(" ")
      // foreach filter, test if a property matches
      for (let filter of filters) {
        let matching = false;
        const regex = new RegExp(filter.normalize("NFD").replace(/[\u0300-\u036f]/g, ""), "i");
        for (let property of this.columnsToFilter) {
          if (Object.prototype.hasOwnProperty.call(row, property)) {
            if (`${row[property]}`.normalize("NFD").replace(/[\u0300-\u036f]/g, "").match(regex)) {
              matching = true;
            }
          }
        }
        // no property matches the filter
        if (!matching) {
          return false
        }
      }
      // every filter got a match
      return true
    },

    displayTheRightData: function () {
      //change data inside table according to the radio button value

      //reinitialization
      this.localStore.selectedUsers = []
      this.items = []
      this.headCheckbox = false

      const usersId = Object.keys(localStore.users)
      for (let i = 0; i < usersId.length; i++) {
        const data = localStore.users[usersId[i]]
        data.id = usersId[i]
        switch (this.radioSelected) {
          case 'both':
            this.items.push(data)
            break;
          case 'withOlvidId':
            if (typeof localStore.users[usersId[i]]['olvidIdentity'] !== 'undefined') {
              this.items.push(data)
            }
            break;
          case 'withoutOlvidId':
            if (typeof localStore.users[usersId[i]]['olvidIdentity'] === 'undefined') {
              this.items.push(data)
            }
            break;
        }
      }
    },

    singleRevocation: function (id, username, invitationLink) {
      //manage the single revocation (with the button on the column revoke)

      //this will set message on the second modal to know which kind of revocation is selected
      this.revocationType = store.state.dictionary.revoke.revokeIdentityKey
      this.revokeIdentityDisabled = (typeof invitationLink === 'undefined')
      if (typeof invitationLink === 'undefined') {
        this.revocationType = store.state.dictionary.revoke.removeUserKey
        this.revokeIdentityDisabled = true

      } else {
        this.revokeIdentityDisabled = false
      }

      this.toRevoke = []
      this.toRevoke.push(id)
      this.localStore.textToDisplay = username
      this.showModal('revocationModal')
    },

    multipleRevocations: function () {
      //manage multiple revocations (with the button below the table, managed by the checkboxes)
      this.toRevoke = []
      this.revocationType = store.state.dictionary.revoke.revokeIdentityKey
      this.revokeIdentityDisabled = false
      this.localStore.textToDisplay = localStore.selectedUsers.length + " " + store.state.dictionary.users

      for (let i = 0; i < localStore.selectedUsers.length; i++) {
        this.toRevoke.push(localStore.selectedUsers[i])
        if (typeof localStore.users[localStore.selectedUsers[i]].invitationLink === 'undefined') {
          //if there is at least one user without olvid id => do not allow revocation type 0
          this.revokeIdentityDisabled = true
          this.revocationType = store.state.dictionary.revoke.removeUserKey
        }
      }

      this.showModal('revocationModal')
    },

    setRevocationType: function (type) {
      this.revocationType = type
    },

    makeToast(variant = null, delay, text, title) {
      this.$bvToast.toast(text, {
        title: title,
        variant: variant,
        toaster: 'b-toaster-top-center',
        autoHideDelay: delay,
        solid: true
      })
    },

    launchSignOutRequest: function (userId) {
      store.state.loading = true
      var userToSignOut = []
      if (userId !== null) {
        userToSignOut.push(userId)
      } else {
        userToSignOut = this.localStore.selectedUsers
      }

      let promises = [];
      userToSignOut.forEach((id) => {
        promises.push(api.signOutUserRequest(localStore.realmAsked, id))
      })

      Promise.all(promises)
          .then((result) => {
                if (result.length > 1) {
                  this.makeToast('success', 5000, store.state.dictionary.toastSignOutDone.textPlural, store.state.dictionary.toastSignOutDone.title)
                } else {
                  this.makeToast('success', 5000, store.state.dictionary.toastSignOutDone.textSingular, store.state.dictionary.toastSignOutDone.title)

                }
                this.launchGetUsersListRequest()
                store.state.loading = false
              }
          )
          .catch((error) => {
                console.log(error)
                this.makeToast('danger', 5000, store.state.dictionary.toastDanger.text, store.state.dictionary.toastDanger.title)
                store.state.loading = false
              }
          )
    },

    launchRevocationRequest: function () {
      store.state.loading = true
      var revocation = -1
      if (this.revocationType === store.state.dictionary.revoke.revokeIdentityKey)
        revocation = 0
      else if (this.revocationType === store.state.dictionary.revoke.removeUserKey)
        revocation = 1

      let promises = [];
      this.toRevoke.forEach((id) => {
        promises.push(api.revokeOneUserRequest(localStore.realmAsked, id, revocation))
      })

      Promise.all(promises)
          .then(() => {
                this.launchGetUsersListRequest()
                this.makeToast('success', 5000, store.state.dictionary.toastRevocationDone.text, store.state.dictionary.toastRevocationDone.title)
                store.state.loading = false
              }
          )
          .catch((error) => {
                console.log(error)
                store.state.loading = false
                this.makeToast('danger', 5000, store.state.dictionary.toastDanger.text, store.state.dictionary.toastDanger.title)
              }
          )
    },

    launchGetUsersListRequest: function () {
      store.state.loading = true
      const realmName = localStore.realmAsked
      this.storeState.radioButtonOlvidIdState = this.radioSelected

      this.filter = ""

      api.getUsersListRequest(realmName, this.filters, this.currentPage)
          .then(
              (result) => {
                if (typeof result.queryNotAllowed === undefined || !result.queryNotAllowed) {
                  this.localStore.users = result.userList
                  this.localStore.count = result.count
                  this.localStore.writeAllowed = result.writeAllowed
                  store.state.loading = false
                  this.initializationTable()
                  store.state.loading = false
                  //add remove and revoke buttons in table
                  if (localStore.writeAllowed) {
                    this.fields.unshift(
                        {
                          key: 'selectUsers',
                          label: '',
                          thStyle: {width: '30px'},
                          thClass: 'align-middle',

                        },)
                    this.fields.push({
                      key: 'signOut',
                      label: store.state.dictionary.revoke.signOutLabel,
                      thStyle: {width: '130px'},
                      thClass: 'align-middle',

                    },)
                    this.fields.push({
                      key: 'isrevoked',
                      label: store.state.dictionary.fieldTable.revoke,
                      thStyle: {width: '100px'},
                      thClass: 'align-middle',

                    },)
                  }
                }
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },

    initializationTable: function () {
      localStore.selectedUsers = []
      this.displayTheRightData()

    },
  },
  beforeMount() {
    localStore.realmAsked = this.$route.params.realmName
    this.launchGetUsersListRequest()
  },
}
</script>

<style scoped>
.revocationButton{
  float: right;
  margin-top: 10px;
  margin-right: 25px;
}
.buttons {
  float: right;
}
.buttons > * {
  margin-left: 10px;
}
.apikey {
  font-size: 60%;
}
</style>
