<template>
  <div id="app">
    <PageAuthenticationInProgress v-if="store.state.authentication"></PageAuthenticationInProgress>
    <v-breakpoint v-else>
      <TopBanner id="topbanner"></TopBanner>
      <div>
        <LeftMenu id="navbar"></LeftMenu>
        <router-view :key="$route.fullPath"></router-view>
      </div>
    </v-breakpoint>
  </div>
</template>

<script>
import {store} from "@/main";
import LeftMenu from "./components/LeftMenu";
import TopBanner from "@/components/TopBanner";
import PageAuthenticationInProgress from "./components/PageAuthenticationInProgress";

export default {
  name: 'App',
  components: {LeftMenu, TopBanner, PageAuthenticationInProgress},
  metaInfo: {
    title: 'Olvid Admin Console'
  },
  data() {
    return {
      store : store,
      screenWidth:window.innerWidth
    }
  },
  methods:{

    resizeHandler:function(){
      this.screenWidth=window.innerWidth
    },
    beforeUnloadHandler(e) {
      if (store.state.dontLeave) {
        e.preventDefault();
        e.returnValue = ""
      }
    },
    removeEventListener() {
      window.removeEventListener("beforeunload", this.beforeUnloadHandler);
    }
  },
  created() {
    window.addEventListener("resize", this.resizeHandler);
    //user leaving page (closing tab, reload button...)
    window.addEventListener("beforeunload", this.beforeUnloadHandler);
    window.addEventListener("unload", this.removeEventListener);
  },
  beforeMount(){
    // call authenticaitondiscovery in authentication component
    store.state.authentication = true
  },
  beforeDestroy() {
    this.removeEventListener()
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeHandler);
  },
}
</script>

