      <!-- Left aligned nav items -->
      <div class="navbar-nav mr-auto">
        <ButtonBurger id="burgerbutton"></ButtonBurger>
        <span id="realmpart" v-if="storeState.authenticationInfo.realm && storeState.authenticationInfo.realm==='master'">
          <strong>{{ storeState.dictionary.topBannerRealmPart.realm }}</strong>
            <span>{{ storeState.authenticationInfo.realm.substring(0,15) }}</span>
            <span v-show="storeState.authenticationInfo.realm.length>16">…</span>
        </span>
        <span v-else-if="!storeState.authenticationInfo.realm" style="margin-left: 25px">
          {{ storeState.dictionary.topBannerRealmPart.notlogged }}
        </span>
      </div>

      <!-- Center aligned nav items-->
