              <span class="whitecontent">
                <font-awesome-icon icon="user" class="whiteicon"/>
                <span id="bigScreenShowUsernameInDropdown">
                  {{storeState.authenticationInfo.username.substring(0,15)}}
                  <span v-show="storeState.authenticationInfo.username.length>16">…</span>
                </span>
              </span>
            </template>
              <span id="smallScreenShowUsernameInDropdownHeader">
                <b-dropdown-header>
                  <font-awesome-icon icon="user" style="margin-right: 6px;"/>
                  {{ storeState.authenticationInfo.username }}
                </b-dropdown-header>
                <b-dropdown-divider/>
              </span>
               <b-dropdown-item @click="launchRedirectToAccount">
                 <font-awesome-icon icon="user-circle" class="profilicon"/>
                 {{ storeState.dictionary.dropdownItemTopBannerProfile.account }}
               </b-dropdown-item>
              <b-dropdown-divider/>
              <b-dropdown-item @click="signOut">
                <font-awesome-icon icon="sign-out-alt" class="profilicon"/>
                {{ storeState.dictionary.dropdownItemTopBannerProfile.signout }}
              </b-dropdown-item>
            </b-dropdown>
           </span>


          <!--email management-->
          <span style="margin-left: 10px" v-show="storeState.authenticationInfo.email">
            <!-- small screen -->
            <b-dropdown id="smallScreenShowEmailInDropdownHeader" size="sm" right no-caret variant="outline-primary">
              <template slot="button-content">
                <font-awesome-icon icon="envelope" class="whiteicon"/>
              </template>
              <b-dropdown-header>
                <span v-if=" typeof storeState.authenticationInfo.email !== 'undefined'">
                  <font-awesome-icon icon="envelope" style="margin-right: 6px;"/>
                    <a :href="'mailto:' + storeState.authenticationInfo.email" id="mailto1">
                      {{storeState.authenticationInfo.email.substring(0,15)}}
                    </a>
                  <span v-show="storeState.authenticationInfo.email.length>16">…</span>
