<script>
import ButtonBurger from "./ButtonBurger";
import {store} from "@/main";
import {api} from "../API/API";

export default {
  name: "TopBanner.vue",
  components:{ButtonBurger},
  data(){
    return {
      storeState: store.state,
      screenWidth : window.innerWidth,
    }
  },

  methods:{
    resizeHandler:function(){
      this.screenWidth=window.innerWidth
    },
    signOut:function (){
      api.signOut().catch(function (error) {
        console.log(error)
      })
    },
    launchRedirectToAccount:function(){
      if(store.state.authenticationInfo.realm!=='master'){
        window.location.href = "/auth/realms/" + store.state.authenticationInfo.redirectRealm + "/account/#"
      }
      else if (store.state.authenticationInfo.realm==='master'){
        window.location.href ="/auth/admin/master/console/#/realms"

      }
    }
  },
}
</script>

