<template>
  <div id="revocationpage">
    <AlertBadRealmType/>
    <PagePermissionDenied/>
    <b-modal
        scrollable
        id="wipeModal"
        ref="wipeModal"
        :title="storeState.dictionary.titleModalAreYouSure"
        ok-variant="primary"
        button-size="sm"
        :ok-title="storeState.dictionary.wipe"
        @ok="launchWipeDataRequest"
        @cancel="setRevocationIdToWipe('')"
    >
      <p class="my-4">{{ storeState.dictionary.paragraphModalwipeUserRevokedData }}</p>
    </b-modal>

    <div v-show="!storeState.badRealmType && !storeState.permissionDenied">


      <NavbarPageTitle
          :bold-text="localStore.realmAsked"
          :text="storeState.dictionary.titlePageRevocation"
          :save-button-attendance="false"
          :refresh-function="launchGetRevocationLogsRequest"
      >
      </NavbarPageTitle><br><br>

      <Spinner/>

      <b-form-input size="sm" id="filter" :placeholder="storeState.dictionary.placeholderFilter" v-model="idFilter"/>
      <b-table
          class="text-center"
          size="sm"
          :empty-text='storeState.dictionary.tableEmptyText'
          show-empty
          striped hover
          responsive
          :filter="idFilter"
          :items="items"
          :fields="fields"
      >
        <template v-slot:head(deleteData)="index">
          <span v-show="localStore.writeAllowed"> {{ index.label }}</span>
        </template>
        <template v-slot:cell(deleteData)="index">
          <font-awesome-icon
              @click="setRevocationIdToWipe(index.item.id)"
              v-show="localStore.writeAllowed && index.item.username"
              icon="trash"
              v-b-modal.wipeModal
          />
        </template>
        <template v-slot:cell(timestamp)="index">
          <span>{{ (new Date(index.item.timestamp)).toLocaleString() }}</span>
        </template>
      </b-table>
    </div>
  </div>
</template>

<script>
import {store} from "@/main";
import {api} from "../API/API";
import NavbarPageTitle from "./NavbarPageTitle";
import AlertBadRealmType from "./AlertBadRealmType";
import PagePermissionDenied from "@/components/PagePermissionDenied";
import Spinner from "./Spinner";
const localStore = {
  revocation:[],
}
export default {
  name: "PageRevocationLogs",
  components: {Spinner, AlertBadRealmType, NavbarPageTitle, PagePermissionDenied},
  data(){
    return{
      localStore: localStore,
      revocationIdToWipe:'',
      storeState: store.state,
      items:[],
      idFilter:'',
      fields: [
        {
          key: 'revocationType',
          label: store.state.dictionary.fieldTable.revocationType,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
        {
          key: 'timestamp',
          label: store.state.dictionary.fieldTable.revocationTimestamp,
          thStyle: {width: '125px'},
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
        {
          key: 'username',
          label: store.state.dictionary.fieldTable.username,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
        {
          key: 'firstname',
          label: store.state.dictionary.fieldTable.firstname,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
        {
          key: 'lastname',
          label: store.state.dictionary.fieldTable.lastname,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,

        },
        {
          key: 'mail',
          label: store.state.dictionary.fieldTable.email,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
        {
          key: 'position',
          label: store.state.dictionary.fieldTable.position,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
        {
          key: 'company',
          label: store.state.dictionary.fieldTable.company,
          thClass: 'align-middle',
          tdClass: 'align-middle',
          sortable: true,
        },
      ],
    }
  },
  methods: {
    initializationTable : function(){
      this.items=Object.values(localStore.revocation)
      this.convertRevocationTypeIntoString()
      const revocation = Object.keys(this.items)
      for(let i=0;i<revocation.length;i++){
        delete this.items[i].realmId
        delete this.items[i].keycloakSignature
        delete this.items[i].olvidId
      }
      if(localStore.writeAllowed){
        this.fields.push({
          key: 'deleteData',
          label: store.state.dictionary.fieldTable.wipeData,
          thStyle: {width: '50px'},
          thClass: 'align-middle',
        },)
      }
    },

    convertRevocationTypeIntoString : function(){
      for(let i=0;i<this.items.length;i++) {
        if (this.items[i].revocationType === 0) {
          this.items[i].revocationType = store.state.dictionary.revoke.revokeIdentityLabel
        } else if (this.items[i].revocationType === 1) {
          this.items[i].revocationType = store.state.dictionary.revoke.removeUserLabel
        }
      }
    },

    makeToast(variant = null,delay,text,title) {
      this.$bvToast.toast(text, {
        title: title,
        variant: variant,
        toaster: 'b-toaster-top-center',
        autoHideDelay: delay,
        solid: true
      })
    },

    setRevocationIdToWipe(id){
      this.revocationIdToWipe=id
    },

    manageRequestResult:function (result){
      this.localStore.revocation = result.revocations
      this.localStore.writeAllowed = result.writeAllowed
      this.initializationTable()
      store.state.loading=false
    },

    launchWipeDataRequest: function(){
      const id = this.revocationIdToWipe
      if(id!==''){
        store.state.loading=true
        api.wipeUserDataOnOlvidRevocationDatabaseRequest(localStore.realmAsked,id)
            .then(
                (result) => {
                    this.manageRequestResult(result)
                    this.makeToast('success',4000,store.state.dictionary.toastDataWipe.text,store.state.dictionary.toastDataWipe.title)
                }
            )
            .catch(
                (error) =>  {
                    console.log(error)
                    store.state.loading=false
                    this.makeToast("danger",4000,store.state.dictionary.toastDanger.text,store.state.dictionary.toastDanger.title);
                }
            )
      }
    },

    launchGetRevocationLogsRequest : function() {
      store.state.loading = true
      this.idFilter = ''
      api.getRevocationsListRequest(localStore.realmAsked)
          .then(
              (result) => {
                if (typeof result.queryNotAllowed === undefined || !result.queryNotAllowed) {
                  this.manageRequestResult(result)
                }
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading = false
          })
    },
  },

  beforeMount() {
    localStore.realmAsked = this.$route.params.realmName
    this.launchGetRevocationLogsRequest()
  }
}
</script>

<style scoped>
#filter{
  width: 300px;
  margin-bottom: 30px;
}
</style>