<template>
  <div>{{ storeState.dictionary.titlePageAuthentication }}</div>
</template>

<script>
import {store} from "@/main";
import {api} from "../API/API";
import Vue from "vue";

export default {
  name: "PageAuthenticationInProgress",
  data(){
    return{
      storeState:store.state
    }
  },
  beforeMount() {
    //1
    api.authenticationVerification()
      //2
      .then(firstAuthenticationResponse => {
        store.state.authenticationInfo.redirectRealm = firstAuthenticationResponse.realm
        //si master : on save
        if ("master" === firstAuthenticationResponse.realm) {
          store.state.authenticationInfo = Object.assign(store.state.authenticationInfo, firstAuthenticationResponse)
          store.state.authenticationInfo.redirectRealm = firstAuthenticationResponse.realm
          store.state.authentication = false
        }
        //sinon : test sur realm admin
        else {
          //2bis
          api.authenticationVerification(firstAuthenticationResponse.realm).then((secondAuthenticationResponse) => {
            //si objet vide : pas auth
            if (Object.keys(secondAuthenticationResponse).length === 0) {
              store.state.authentication = false
              window.location.href = "/auth/realms/" + store.state.authenticationInfo.redirectRealm + "/account/#/olvid-console"
            }
            //sinon : on save
            else {
              store.state.authenticationInfo = Object.assign(store.state.authenticationInfo, secondAuthenticationResponse)
              store.state.authenticationInfo.redirectRealm = secondAuthenticationResponse.realm
              store.state.authentication = false
            }
          })
        }
      })
      //3
      .then(() => {
        //on lance les realms requests
        api.getRealmsMainAttributesRequest()
            .then(
                (result) => {
                  Vue.set(store.state, "realmMainData", result)
                }
            )
            .catch((error) => {
              console.log(error)
              })
      })
      //4
      .catch((error) => {console.log(error)})
  }
}
</script>

