<template>
  <div>
    <div id="left-menu" :class="updateLeftMenuAfterSaved">

      <!-- 'Realms' Row-->
      <ul>
        <li :class="className['/']"
            id="realmNavbar"
            @click="changeRoute(null,0)">
          <strong>{{ storeState.dictionary.titlePageRealms }}</strong>
        </li>
        <li class="fake" v-if="storeState.realmMainData.logsAndSettingsAccess">
          <ul>
            <li @click="changeRoute(null,7)" :class="className['/realmsConfiguration']" v-show="storeState.realmMainData.logsAndSettingsAccess">{{ storeState.dictionary.leftMenuSubtitle.realmsConfig }}</li>
            <li @click="changeRoute(null,8)" :class="className['/eventLogs']">{{ storeState.dictionary.leftMenuSubtitle.eventLogs }}</li>
          </ul>
        </li>
      </ul>


      <!-- Admin Realm Row-->
      <ul v-for="(realm,index) in storeState.realmMainData.realmMainAttributes" :key="index">
        <li v-if="realm.admin" :class="className['/' + realm.name + '/admin']"  @click="changeRoute(realm.name,1)">
          <strong>
            {{ realm.name }} <font-awesome-icon icon="lock" style="font-size: 15px;margin-left: 5px"/>
          </strong>
        </li>
      </ul>

      <!-- Olvid Realms Rows and Sub-Rows-->
      <ul v-for="(realm,index) in storeState.realmMainData.realmMainAttributes" :key="index">
        <li v-if="realm.olvid" :class="className['/' + realm.name + '/dashboard']" @click="changeRoute(realm.name,2)">
          <strong>{{ realm.name }}</strong>
        </li>
        <li class="fake" v-if="realm.olvid">
          <ul>
            <li @click="changeRoute(realm.name,3)" :class="className['/' + realm.name+'/users']">{{ storeState.dictionary.leftMenuSubtitle.users }}</li>
            <li @click="changeRoute(realm.name,4)" :class="className['/' + realm.name+'/revocation']">{{ storeState.dictionary.leftMenuSubtitle.revocation }}</li>
            <li @click="changeRoute(realm.name,5)" :class="className['/' + realm.name+'/settings']">{{ storeState.dictionary.leftMenuSubtitle.settings }} </li>
            <li @click="changeRoute(realm.name,6)" :class="className['/' + realm.name+'/groups']">{{ storeState.dictionary.leftMenuSubtitle.groups }} </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import {store} from "@/main";
import router from "../router";
import Vue from "vue";
export default {
  name: "LeftMenu",
  data: function () {
    return {
      storeState: store.state,
      className: [], //to show which realm is active, thanks to a grey bar on the left menu
    }
  },

  methods: {
    highlightActiveItem: function() {
      for (let i in this.className) {
        Vue.set(this.className, i, '')
      }
      Vue.set(this.className, decodeURI(this.$route.path), 'active')
    },
    changeRoute: function (realmName, componentToDisplay) {
      let newRoute = ''
      switch (componentToDisplay) {
        case 0: //BasePage
          newRoute = ''
          break;
        case 1: //PageAdminsList
          newRoute = realmName + '/admin'
          break;
        case 2: //PageOlvidDashboard
          newRoute = realmName + '/dashboard'
          break;
        case 3: //PageUsersList
          newRoute = realmName + '/users'
          break;
        case 4: //PageRevocationLogs
          newRoute = realmName + '/revocation'
          break;
        case 5: //PageSecuritySettings
          newRoute = realmName + '/settings'
          break;
        case 6: //PageGroups
          newRoute = realmName + '/groups'
          break;
        case 7: //PageRealmsConfiguration
          newRoute = 'realmsConfiguration'
          break;
        case 8: //PageEventLogs
          newRoute = 'eventLogs'
          break;
      }
      if (this.currentRoute !== newRoute) {
        this.currentRoute = newRoute
        router.push(`/${newRoute}`)
      }
    },
  },

  computed: {
    realmMainData() {
      return this.storeState.realmMainData
    }
  },

  watch: {
    '$route' () {
      this.highlightActiveItem()
    },
    realmMainData () {
      this.highlightActiveItem()
    }
  },
}

</script>

