<template>
  <div id="realmlist">
    <AlertBadRealmType/>
    <PagePermissionDenied/>
    <Spinner/>
    <b-container v-show="!storeState.badRealmType && !storeState.permissionDenied">
      <NavbarPageTitle
          :bold-text="storeState.dictionary.leftMenuSubtitle.realmsConfig"
          :no-change="noChangesHaveBeenDone"
          :save-button-attendance="true"
          :save-function="verificationBeforeSave"
          :refresh-function="launchGetRealmsConfigurationRequest"
      />
      <br>
      <b-container id="realms" class="bv-example-row">
        <div class="centered"><span id="enablePrometheusMetrics">
          <b-form-checkbox v-model="localStore.enablePrometheusMetrics" switch v-b-tooltip.hover.bottom="{title:storeState.dictionary.tooltipEnablePrometheusMetrics}">{{storeState.dictionary.checkboxEnablePrometheusMetrics.part1}}
            <a :href="localStore.baseUrl + '/auth/realms/master/olvid-rest/olvidMetrics'" target="_blank">{{localStore.baseUrl}}/auth/realms/master/olvid-rest/olvidMetrics</a>{{storeState.dictionary.checkboxEnablePrometheusMetrics.part2}}<font-awesome-icon
                class="rightquestioncircleicon"
                icon="question-circle"/>
          </b-form-checkbox>
        </span>
        </div>
        <div id="realminfo" v-for="(realm) in localStore.initialRealmConfig" :key="realm.name">
            <br><br>
            <b-row>
              <b-col>
                <strong id="realmname" v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.realmAdminOrOlvid }">
                  {{ realm.name }}<font-awesome-icon class="rightquestioncircleicon" icon="question-circle"/>
                </strong>
              </b-col>
            </b-row>
            <b-row class="informationmissing" v-show="isInformationMissing">
              {{ informationMissing[realm.name]}}
            </b-row>

            <b-row id="realmsettings" >

              <!-- Checkbox Management-->
              <b-col class="admincol" :id="'col'+localStore.realmsInfo[realm.name].admin" cols="4">
                <!--admin checkbox-->
                <b-form-checkbox
                    class="m-md-2"
                    @change="verificationAfterAdminHasBeenChecked(realm.name)"
                    v-model="localStore.realmsInfo[realm.name].admin"
                    name="checkbox-admin"
                    :disabled="storeState.authenticationInfo.realm !== 'master'"
                    v-b-tooltip.hover.v-light="{
                          title:storeState.dictionary.tooltipCannotModifyRealmAdmin,
                          disabled:storeState.authenticationInfo.realm === 'master'}"
                >
                  {{ storeState.dictionary.typeRealms.admin }} <font-awesome-icon icon="lock" style="font-size: 10px"/>
                </b-form-checkbox>
              </b-col>

              <b-col class="olvidcol" :id="'col'+localStore.realmsInfo[realm.name].olvid" cols="8">
                <!--olvid checkbox-->
                <b-row  cols="1" cols-sm="1" cols-md="2" cols-lg="3">
                  <b-col>
                    <b-form-checkbox
                        class="m-md-2"
                        @change="verificationAfterOlvidHasBeenChecked(realm.name)"
                        :disabled="!(Object.keys(realm.clientsInfo).length > 0) || storeState.authenticationInfo.realm === realm.name"
                        v-model="localStore.realmsInfo[realm.name].olvid"
                        name="checkbox-olvid"
                        v-b-tooltip.hover.v-light="{
                          title:(!Object.keys(realm.clientsInfo).length > 0) ? storeState.dictionary.tooltipNoClienthasBeenFound : storeState.dictionary.tooltipCannotModifyRealmAdmin,
                          disabled:Object.keys(realm.clientsInfo).length > 0 && storeState.authenticationInfo.realm !== realm.name}"
                    >
                      {{ storeState.dictionary.typeRealms.olvid }}
                    </b-form-checkbox>
                  </b-col>
                  <!------------------------->

                  <!-- olvid clients dropdown -->
                  <b-col>
                    <!-- if there are clients -->
                    <b-dropdown
                        v-if="Object.keys(realm.clientsInfo).length > 0 "
                        :disabled="!localStore.realmsInfo[realm.name].olvid"
                        right
                        :text="localStore.realmsInfo[realm.name].olvidClient == null ? storeState.dictionary.dropdownLabelSetClient : localStore.realmsInfo[realm.name].olvidClient"
                        :variant="localStore.realmsInfo[realm.name].olvidClient == null ? '' : 'primary'"
                        size="sm"
                        :key="realm.name"
                        class="m-md-2"
                        v-b-tooltip.hover.v-light="{
                          title:storeState.dictionary.tooltipCheckOlvid,
                          disabled:localStore.realmsInfo[realm.name].olvid}"
                    >
                      <b-dropdown-header v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.selectAClient }">
                        {{ storeState.dictionary.dropdownHeaderSetClient }}
                        <font-awesome-icon class="rightquestioncircleicon" icon="question-circle"/>
                      </b-dropdown-header>
                      <span v-for="(client,index) in Object.keys(localStore.realmsInfo[realm.name].clientsInfo)" :key="index">
                        <b-dropdown-divider/>
                        <b-dropdown-item @click="updateClientsInfoDropdownLabelText(realm.name,client)">
                          {{ client }} <font-awesome-icon v-show="client===localStore.realmsInfo[realm.name].olvidClient" class="checkicon" style="margin-left: 10px;" icon="check"></font-awesome-icon>
                        </b-dropdown-item>
                      </span>
                    </b-dropdown>

                    <!-- else if there is no client at all -->
                    <b-dropdown
                          v-else
                          right
                          :text="storeState.dictionary.dropdownLabelSetClientButNoClient"
                          :disabled=true
                          v-b-tooltip.hover.v-light="{title:storeState.dictionary.tooltipCheckOlvid}"
                          size="sm"
                          :key="realm.name"
                          class="m-md-2"
                    />
                  </b-col>

                  <!-- keyId dropdown -->
                  <b-col>
                    <div>
                      <b-dropdown
                          :disabled="!localStore.realmsInfo[realm.name].olvid"
                          size="sm"
                          :variant="typeof localStore.realmsInfo[realm.name].keyChoosen === 'undefined' ? '' : 'primary'"
                          right
                          :text="typeof localStore.realmsInfo[realm.name].keyChoosen === 'undefined' ? storeState.dictionary.dropdownLabelSetKey : (localStore.realmsInfo[realm.name].keyId[localStore.realmsInfo[realm.name].keyChoosen].keyAlgorithm + ' ('+localStore.realmsInfo[realm.name].keyChoosen.substring(0,4)+'…)')"
                          class="m-md-2"
                          v-b-tooltip.hover.v-light="{
                          title:storeState.dictionary.tooltipCheckOlvid,
                          disabled:localStore.realmsInfo[realm.name].olvid}"
                      >

                        <b-dropdown-header v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.selectAKey }">
                          {{ storeState.dictionary.dropdownHeaderSetKey }}
                          <font-awesome-icon class="rightquestioncircleicon" icon="question-circle"/>
                        </b-dropdown-header>
                        <span :key="index" v-for="(key,index) in localStore.realmsInfo[realm.name].keyId">
                          <b-dropdown-divider/>
                          <b-dropdown-item  @click="updateKeyIdDropdownLabelText(realm.name,index)" >
                              <strong>{{ storeState.dictionary.dropdownItemSetKey.algorithm }}</strong>{{ key.keyAlgorithm }}
                              <strong style="margin-left: 20px">{{ storeState.dictionary.dropdownItemSetKey.type }}</strong>{{ key.keyType }}
                              <font-awesome-icon v-show="index===localStore.realmsInfo[realm.name].keyChoosen" style="margin-left: 25px;" class="checkicon" icon="check"></font-awesome-icon><br>
                              <strong>{{ storeState.dictionary.dropdownItemSetKey.id }}</strong><span style="font-size: 10px">{{ index }}</span>
                          </b-dropdown-item>
                        </span>
                      </b-dropdown>
                    </div>
                  </b-col>
                </b-row>


                <!-- Gestion du Form -->
                <b-row style="text-align: justify">
                  <b-col v-show="localStore.realmsInfo[realm.name].olvid" class="bcolRealm">
                    <div>
                      <b-form class="m-md-2">
                        <b-form-group label-for="inputOlvidServerUrl">
                          <slot name="label">
                            <span v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.inputOlvidServerUrl }">
                              {{ storeState.dictionary.labelInputOlvidServerUrl }}
                              <font-awesome-icon class="rightquestioncircleicon" icon="question-circle"/>
                            </span>
                          </slot>
                          <b-form-input @update="verificationAfterFormInputHasBeenChanged(realm.name)" v-model="localStore.realmsInfo[realm.name].olvidServerUrl" type="text" :placeholder="storeState.dictionary.placeholderInputUrl"/>
                        </b-form-group>

                        <b-form-group label-for="inputServerUrl">
                          <slot name="label">
                            <span v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.inputServerUrl }">
                              {{ storeState.dictionary.labelInputServerUrl }}
                              <font-awesome-icon class="rightquestioncircleicon" icon="question-circle" />
                            </span>
                          </slot>
                          <b-form-input @update="verificationAfterFormInputHasBeenChanged(realm.name)" v-model="localStore.realmsInfo[realm.name].keycloakServerUrl" :placeholder="storeState.dictionary.placeholderInputUrl"/>
                        </b-form-group>

                        <b-form-group label-for="inputKeycloakApiKey">
                          <slot name="label">
                            <span v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.inputApiKey }">
                              {{ storeState.dictionary.labelInputApiKey }}
                              <font-awesome-icon class="rightquestioncircleicon" icon="question-circle"/>
                            </span>
                          </slot>
                          <b-form-input @update="verificationAfterFormInputHasBeenChanged(realm.name)" v-model="localStore.realmsInfo[realm.name].keycloakApiKey" :placeholder="storeState.dictionary.placeholderInputApiKey"/>
                        </b-form-group>

                        <input @change="setRevocationAllowedValue('revocation'+realm.name,realm.name)" type="checkbox" :id="'revocation'+realm.name" name="revocationAllowed" :checked="localStore.realmsInfo[realm.name].revocationAllowed">
                        <label class="revocationAllowedLabel" :for="'revocation'+realm.name" v-b-tooltip.hover.right="{title:storeState.dictionary.tooltipRealmItems.revocationAllowed }">
                          {{ storeState.dictionary.checkboxRevocationAllowed }}
                          <font-awesome-icon class="rightquestioncircleicon" icon="question-circle" />
                        </label>
                      </b-form>
                    </div>
                  </b-col>
                </b-row>
              </b-col>
              <!----------------->
            </b-row>
        </div>
      </b-container>
      <b-modal
          scrollable
          ref="saveConfirmationModal"
          :title="storeState.dictionary.titleModalAreYouSure"
          :ok-title="storeState.dictionary.buttonLabelSave"
          ok-variant="success"
          @ok="launchSaveRequest">
        <p class="my-4">{{ storeState.dictionary.paragraphModalChangesHaveBeenDone.text }}</p>
        <strong>{{ storeState.dictionary.paragraphModalChangesHaveBeenDone.titleList }}</strong><br><br>

        <span v-if="typeof changes['enablePrometheusMetrics'] !== 'undefined' && changes['enablePrometheusMetrics'].length > 0">
          <b-table
                   :items="changes['enablePrometheusMetrics']"
                   :fields="fields"
                   style="text-align:center"
                   striped hover caption-top small responsive>
          </b-table>
        </span>
        <span :key="realm" v-for="realm in localStore.initialRealmConfig">
          <b-table v-if="typeof changes[realm.name] !== 'undefined' && changes[realm.name].length > 0"
                   :items="changes[realm.name]"
                   :fields="fields"
                   style="text-align:center"
                   striped hover caption-top small responsive>
            <template  #table-caption>{{realm.name}}</template>
          </b-table>
          <br v-if="typeof changes[realm.name] !== 'undefined' && changes[realm.name].length > 0">
        </span>
        <br>
      </b-modal>
    </b-container>
  </div>

</template>

<script>
import {store} from "@/main";
import {api} from "../API/API";
import NavbarPageTitle from "./NavbarPageTitle";
import Spinner from "./Spinner";
import Vue from "vue";
import AlertBadRealmType from "@/components/AlertBadRealmType";
import PagePermissionDenied from "@/components/PagePermissionDenied";

const localStore = {
  realmsInfo: {},
  enablePrometheusMetrics: false,
  baseUrl: location.protocol + '//' + location.host,
  initialRealmConfig: {}
}
export default {
  name: "PageRealmsConfiguration",
  components: {Spinner, NavbarPageTitle, AlertBadRealmType, PagePermissionDenied},
  data: function() {
    return {
      storeState: store.state,
      localStore:localStore, // copy of store state value
      isInformationMissing: false, //to know if info is missing
      informationMissing: {}, //message of what is missing, for each realm
      changes: {},//this object is used in save confirmation to display list of changes
      fields: [
        {key: 'item', label: store.state.dictionary.realmSaveManagement.changes.item, thStyle: {width: '33%'}},
        {key: 'before', label: store.state.dictionary.realmSaveManagement.changes.before, thStyle: {width: '33%'}},
        {key: 'after', label: store.state.dictionary.realmSaveManagement.changes.after, thStyle: {width: '33%'}},
      ],
    }
  },

  methods: {

    makeToast(variant = null, delay, text, title) {
      this.$bvToast.toast(text, {
        title: title,
        variant: variant,
        toaster: 'b-toaster-top-center',
        autoHideDelay: delay,
        solid: true
      })
    },

    verificationBeforeSave: function () {
      //Object construction containing realms' config
      this.isInformationMissing = false
      this.informationMissing = []
      let olvidClientMissing = false;
      let keyMissing = false

      const keys = Object.keys(this.localStore.realmsInfo)
      for (const key of keys) {
        //no client and no key
        if (this.localStore.realmsInfo[key].olvid && (typeof this.localStore.realmsInfo[key].keyChoosen === 'undefined') && (typeof this.localStore.realmsInfo[key].olvidClient === 'undefined')) {
          olvidClientMissing = true;
          keyMissing = true
          this.informationMissing[key] = store.state.dictionary.realmSaveManagement.informationMissing.both
        } else if (this.localStore.realmsInfo[key].olvid && (typeof this.localStore.realmsInfo[key].olvidClient === 'undefined')) {
          //no client
          olvidClientMissing = true;
          this.informationMissing[key] = store.state.dictionary.realmSaveManagement.informationMissing.client
        } else if (this.localStore.realmsInfo[key].olvid && (typeof this.localStore.realmsInfo[key].keyChoosen === 'undefined')) {
          //no key
          keyMissing = true
          this.informationMissing[key] = store.state.dictionary.realmSaveManagement.informationMissing.key
        }
      }

      if (!olvidClientMissing && !keyMissing) {
        this.isInformationMissing = false
        this.computeWhatWasChanged()
        this.showModal()
        return
      }

      // modal box information is missing
      this.isInformationMissing = true
      let text;
      if (olvidClientMissing && keyMissing) {
        text = store.state.dictionary.toastSaveWarningIncomplete.text.part1 + store.state.dictionary.realmSaveManagement.informationMissing.modal.both + store.state.dictionary.toastSaveWarningIncomplete.text.part2
      } else if (olvidClientMissing) {
        text = store.state.dictionary.toastSaveWarningIncomplete.text.part1 + store.state.dictionary.realmSaveManagement.informationMissing.modal.client + store.state.dictionary.toastSaveWarningIncomplete.text.part2
      } else {
        text = store.state.dictionary.toastSaveWarningIncomplete.text.part1 + store.state.dictionary.realmSaveManagement.informationMissing.modal.key + store.state.dictionary.toastSaveWarningIncomplete.text.part2
      }
      const title = store.state.dictionary.toastSaveWarningIncomplete.title
      this.makeToast('warning', 7000, text, title)
    },

    showModal() {
      this.$refs['saveConfirmationModal'].show()
    },

    hideModal() {
      this.$refs['saveConfirmationModal'].hide()
    },

    removeEmptyStringInObject: function () {
      const realms = Object.keys(this.localStore.realmsInfo)
      for (let i = 0; i < realms.length; i++) {
        if (typeof this.localStore.realmsInfo[realms[i]].olvidServerUrl !== 'undefined')
          if (this.localStore.realmsInfo[realms[i]].olvidServerUrl.trim().length === 0)
            delete this.localStore.realmsInfo[realms[i]].olvidServerUrl
        if (typeof this.localStore.realmsInfo[realms[i]].keycloakServerUrl !== 'undefined')
          if (this.localStore.realmsInfo[realms[i]].keycloakServerUrl.trim().length === 0)
            delete this.localStore.realmsInfo[realms[i]].keycloakServerUrl
        if (typeof this.localStore.realmsInfo[realms[i]].keycloakApiKey !== 'undefined')
          if (this.localStore.realmsInfo[realms[i]].keycloakApiKey.trim().length === 0)
            delete this.localStore.realmsInfo[realms[i]].keycloakApiKey
      }

    },

    computeWhatWasChanged: function () {
      const realm = Object.keys(localStore.initialRealmConfig)
      Vue.set(this.changes, "enablePrometheusMetrics", [])
      if (store.state.enablePrometheusMetrics !== this.localStore.enablePrometheusMetrics) {
        const data = {item: '', before: '', after: ''}
        data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.enablePrometheusMetrics
        data.before = store.state.enablePrometheusMetrics
        data.after = this.localStore.enablePrometheusMetrics
        this.changes["enablePrometheusMetrics"].push(data)
      }

      for (let i = 0; i < realm.length; i++) {
        Vue.set(this.changes, realm[i], [])

        //admin and olvid changes
        if (localStore.initialRealmConfig[realm[i]].admin) {
          if (!this.localStore.realmsInfo[realm[i]].admin) {
            if (this.localStore.realmsInfo[realm[i]].olvid) {
              //before: admin | after: olvid
              const data = {item: '', before: '', after: ''}
              data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.realmStatus
              data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusAdmin
              data.after = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusOlvid
              this.changes[realm[i]].push(data)
            } else {
              //before: admin | after: nothing
              const data = {item: '', before: '', after: ''}
              data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.realmStatus
              data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusAdmin
              data.after = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusNoStatus
              this.changes[realm[i]].push(data)
            }
          }
        } else if (localStore.initialRealmConfig[realm[i]].olvid) {
          if (!this.localStore.realmsInfo[realm[i]].olvid) {
            if (this.localStore.realmsInfo[realm[i]].admin) {
              //before: olvid | after: admin
              const data = {item: '', before: '', after: ''}
              data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.realmStatus
              data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusOlvid
              data.after = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusAdmin
              this.changes[realm[i]].push(data)
            } else {
              //before: olvid | after: nothing
              const data = {item: '', before: '', after: ''}
              data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.realmStatus
              data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusOlvid
              data.after = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusNoStatus
              this.changes[realm[i]].push(data)
            }
          }
        } else {
          if (this.localStore.realmsInfo[realm[i]].admin) {
            //before: no status | after: admin
            const data = {item: '', before: '', after: ''}
            data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.realmStatus
            data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusNoStatus
            data.after = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusAdmin
            this.changes[realm[i]].push(data)
          } else if (this.localStore.realmsInfo[realm[i]].olvid) {
            //before: no status | after: olvid
            const data = {item: '', before: '', after: ''}
            data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.realmStatus
            data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusNoStatus
            data.after = store.state.dictionary.realmSaveManagement.changes.itemsValue.realmStatusOlvid
            this.changes[realm[i]].push(data)
          }
        }
        //revocation allowed changes
        if (localStore.initialRealmConfig[realm[i]].revocationAllowed !== this.localStore.realmsInfo[realm[i]].revocationAllowed) {
          const data = {item: '', before: '', after: ''}
          data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.revocationAllowed
          data.before = localStore.initialRealmConfig[realm[i]].revocationAllowed
          data.after = this.localStore.realmsInfo[realm[i]].revocationAllowed
          this.changes[realm[i]].push(data)
        }

        //client changes
        if (localStore.initialRealmConfig[realm[i]].olvidClient !== this.localStore.realmsInfo[realm[i]].olvidClient) {
          const data = {item: '', before: '', after: ''}
          data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.olvidClient
          if (typeof localStore.initialRealmConfig[realm[i]].olvidClient === 'undefined')
            data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.olvidClientNoClient
          else
            data.before = localStore.initialRealmConfig[realm[i]].olvidClient
          data.after = this.localStore.realmsInfo[realm[i]].olvidClient
          this.changes[realm[i]].push(data)
        }

        //key changes
        if (localStore.initialRealmConfig[realm[i]].keyChoosen !== this.localStore.realmsInfo[realm[i]].keyChoosen) {
          const data = {item: '', before: '', after: ''}
          data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.keySelected
          if (typeof localStore.initialRealmConfig[realm[i]].keyChoosen === 'undefined')
            data.before = store.state.dictionary.realmSaveManagement.changes.itemsValue.keySelectedNoKey
          else
            data.before = localStore.initialRealmConfig[realm[i]].keyChoosen.substring(0, 10) + '…'
          data.after = this.localStore.realmsInfo[realm[i]].keyChoosen.substring(0, 10) + '…'
          this.changes[realm[i]].push(data)
        }

        //olvidserverurl changes
        if (localStore.initialRealmConfig[realm[i]].olvidServerUrl !== this.localStore.realmsInfo[realm[i]].olvidServerUrl) {
          const data = {item: '', before: '', after: ''}
          data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.olvidMessageDistributionServer
          data.before = localStore.initialRealmConfig[realm[i]].olvidServerUrl
          data.after = this.localStore.realmsInfo[realm[i]].olvidServerUrl
          this.changes[realm[i]].push(data)
        }

        //keycloakServerUrl changes
        if (localStore.initialRealmConfig[realm[i]].keycloakServerUrl !== this.localStore.realmsInfo[realm[i]].keycloakServerUrl) {
          const data = {item: '', before: '', after: ''}
          data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.publicKeycloakUrl
          data.before = localStore.initialRealmConfig[realm[i]].keycloakServerUrl
          data.after = this.localStore.realmsInfo[realm[i]].keycloakServerUrl
          this.changes[realm[i]].push(data)
        }

        //keycloak API Key changes
        if (localStore.initialRealmConfig[realm[i]].keycloakApiKey !== this.localStore.realmsInfo[realm[i]].keycloakApiKey) {
          const data = {item: '', before: '', after: ''}
          data.item = store.state.dictionary.realmSaveManagement.changes.itemsLabel.keycloakApiKey
          data.before = localStore.initialRealmConfig[realm[i]].keycloakApiKey
          data.after = this.localStore.realmsInfo[realm[i]].keycloakApiKey
          this.changes[realm[i]].push(data)
        }
      }
    },

    updateClientsInfoDropdownLabelText: function (name, client) {
      // change the value of the text in the clients info dropdown
      this.$set(localStore.realmsInfo[name], "olvidClient", client)
    },

    updateKeyIdDropdownLabelText: function (name, key) {
      // change the value of the text in the keyId dropdown
      this.$set(localStore.realmsInfo[name], "keyChoosen", key)

    },

    setRevocationAllowedValue: function (element, realmName) {
      if (document.getElementById(element).checked)
        this.localStore.realmsInfo[realmName].revocationAllowed = true
      else
        this.localStore.realmsInfo[realmName].revocationAllowed = false
    },

    verificationAfterAdminHasBeenChecked: function (realmName) {
      //First, if the olvid checkbox of the realm were checked before, put the olvid checkbox value to false, to let admin checkbox true
      if (this.localStore.realmsInfo[realmName].olvid)
        this.localStore.realmsInfo[realmName].olvid = false

      //if there were one other admin realm before, put its admin checkbox value to false, to let this new realm to admin
      const realm = Object.keys(localStore.initialRealmConfig)
      for (let i = 0; i < realm.length; i++) {
        if (this.localStore.realmsInfo[realm[i]].admin && realm[i] !== realmName) {
          this.localStore.realmsInfo[realm[i]].admin = false
        }
      }
    },

    verificationAfterOlvidHasBeenChecked: function (realmName) {
      //if the admin checkbox of the realm were checked before, put the admin checkbox value to false, to let olvid checkbox true
      if (this.localStore.realmsInfo[realmName].admin === true)
        this.localStore.realmsInfo[realmName].admin = false

    },

    verificationAfterFormInputHasBeenChanged: function (realmName) {
      // if no text is in this following attributes, they are gonna be deleted, to do a real comparison between
      //localStore.initialRealmConfig and this.localStore.realmsInfo
      //Otherwise they can't be equals in this case bc on realmInfos an attribute is empty but exists
      // and in realmList it doesn't exist at all
      if (typeof localStore.initialRealmConfig[realmName].olvidServerUrl === 'undefined')
        if (this.localStore.realmsInfo[realmName].olvidServerUrl === "")
          delete this.localStore.realmsInfo[realmName].olvidServerUrl
      if (typeof localStore.initialRealmConfig[realmName].keycloakServerUrl === 'undefined')
        if (this.localStore.realmsInfo[realmName].keycloakServerUrl === "")
          delete this.localStore.realmsInfo[realmName].keycloakServerUrl
      if (typeof localStore.initialRealmConfig[realmName].keycloakApiKey === 'undefined')
        if (this.localStore.realmsInfo[realmName].keycloakApiKey === "")
          delete this.localStore.realmsInfo[realmName].keycloakApiKey

    },

    initializationVerification: function () {
      //if a realm has been created, deleted and created again, it could cause errors, so we verify:
      const realm = Object.keys(this.localStore.realmsInfo)
      for (let i = 0; i < realm.length; i++) {
        var doesKeyChoosenExist = false
        var doesOlvidClientExist = false

        //keyChoosen existence verification
        if (typeof this.localStore.realmsInfo[realm[i]].keyChoosen !== 'undefined') {
          const keyId = Object.keys(this.localStore.realmsInfo[realm[i]].keyId)
          for (let j = 0; j < keyId.length; j++) {
            if (this.localStore.realmsInfo[realm[i]].keyChoosen === keyId[j]) {
              doesKeyChoosenExist = true
            }
          }
          if (!doesKeyChoosenExist) {
            delete localStore.initialRealmConfig[realm[i]].keyChoosen
            delete this.localStore.realmsInfo[realm[i]].keyChoosen

          }
        }

        if (typeof this.localStore.realmsInfo[realm[i]].olvidClient !== 'undefined') {
          //OlvidClient existence verification
          const client = Object.keys(this.localStore.realmsInfo[realm[i]].clientsInfo)
          for (let j = 0; j < client.length; j++) {
            if (this.localStore.realmsInfo[realm[i]].olvidClient === client[j]) {
              doesOlvidClientExist = true
            }

          }

          if (!doesOlvidClientExist) {
            delete localStore.initialRealmConfig[realm[i]].olvidClient
            delete this.localStore.realmsInfo[realm[i]].olvidClient
          }
        }
      }

    },

    dataInitialization: function () {
      this.localStore.realmsInfo = JSON.parse(JSON.stringify(localStore.initialRealmConfig)) //give the value only, and not the references => two different parts of the memory
      this.localStore.enablePrometheusMetrics = store.state.enablePrometheusMetrics
      this.initializationVerification()
      this.computeWhatWasChanged()
    },

    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/')
    },

    manageRequestResult: function (result) {
      localStore.initialRealmConfig = result.realmList
      store.state.enablePrometheusMetrics = result.enablePrometheusMetrics
      this.dataInitialization()
    },

    launchMainDataRequest: function () {
      api.getRealmsMainAttributesRequest()
          .then(
              (result) => {
                Vue.set(store.state, "realmMainData", result)
              }
          )
          .catch((error) => {
            console.log(error)
          })
    },

    launchSaveRequest: function () {
      this.removeEmptyStringInObject()
      store.state.loading = true
      api.setRealmsConfigurationRequest(JSON.stringify({
        realmList: this.localStore.realmsInfo,
        enablePrometheusMetrics: this.localStore.enablePrometheusMetrics,
      }))
          .then(
              (result) => {
                this.manageRequestResult(result)
                this.launchMainDataRequest()
                store.state.loading = false
                this.makeToast('success', 4000, store.state.dictionary.toastSaveSuccess.text, store.state.dictionary.toastSaveSuccess.title)
              }
          )
          .catch(
              (error) => {
                console.log(error)
                store.state.loading = false
                this.makeToast("danger", 4000, store.state.dictionary.toastDanger.text, store.state.dictionary.toastDanger.title);
              }
          )
    },

    launchGetRealmsConfigurationRequest: function () {
      store.state.loading = true
      api.getRealmsConfigurationRequest().then(
          result => {
            if (typeof result.queryNotAllowed === undefined || !result.queryNotAllowed) {
              this.manageRequestResult(result)
              store.state.loading = false
            }
          }
      ).catch((error) => {
        console.log(error)
        store.state.loading = false
      })
    },
  },



  computed: {
    username() {
      // We will see what `params` is shortly
      return this.$route.params.username
    },

    noChangesHaveBeenDone() {
      store.state.dontLeave = JSON.stringify(this.localStore.realmsInfo) !== JSON.stringify(localStore.initialRealmConfig) || this.localStore.enablePrometheusMetrics !== store.state.enablePrometheusMetrics
      return !store.state.dontLeave
    },
  },

  beforeMount() {
    this.launchGetRealmsConfigurationRequest()
  }

}
</script>

