<template>
  <div id="olviddashboard" :key="storeState">
    <AlertBadRealmType/>
    <PagePermissionDenied/>
    <div v-show="!storeState.badRealmType && !storeState.permissionDenied">
      <Spinner/>

      <NavbarPageTitle
          :bold-text="localStore.realmAsked"
          :text="storeState.dictionary.titlePageDashboard"
          :save-button-attendance="false"
          :refresh-function="launchOlvidDashboardRequest"
      >
      </NavbarPageTitle>
      <br><br>

        <div id="dashboard-flow">
            <div id="configlinkcontainer">
                <h5>
                  {{ storeState.dictionary.titleItemConfigLink }}
                  <a :href="returnConfigLink" target="_blank">
                    <font-awesome-icon icon="link" style="font-size: 20px" />
                  </a>
                </h5>
              <div id="wrap">
                <iframe id="scaled-iframe" :src="returnConfigLink"/>
              </div>
            </div>

            <div id="nbuserscontainer" :class="somme">
                <h5> {{ storeState.dictionary.titleItemNbUsers }}
                  <span style="color:#007bff ">
                    <strong><em>{{ localStore.dashboardData.count}}</em></strong>
                  </span>
                  <span style="margin-left: 1em">({{ localStore.dashboardData.withOlvidId }} {{ storeState.dictionary.withOlvidId }})</span>
                </h5>
                <pie-chart v-show="localStore.dashboardData.count>0"
                           :class="somme"
                           :key="updatePieChart"
                           :with-olvid-id="localStore.dashboardData.withOlvidId"
                           :without-olvid-id="localStore.dashboardData.withoutOlvidId"
                           id="piechart" />
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import {store} from "../main";
import {api} from "../API/API";
import NavbarPageTitle from "./NavbarPageTitle";
import AlertBadRealmType from "./AlertBadRealmType";
import Spinner from "./Spinner";
import PagePermissionDenied from "@/components/PagePermissionDenied";
const localStore = {
  dashboardData:{},
  realmAsked: "",
}

export default {
  name: "PageOlvidDashboard",
  components: {PagePermissionDenied, Spinner, AlertBadRealmType, NavbarPageTitle},
  props: ['realmName'],
  data: function() {
    return {
      updatePieChart:0,
      storeState: store.state,
      localStore: localStore,
    }
  },
  methods: {

    launchOlvidDashboardRequest:function() {
      store.state.loading=true
      api.getDashboardDataRequest(localStore.realmAsked)
          .then(
              (result) => {
                if (typeof result.queryNotAllowed === undefined || !result.queryNotAllowed) {
                  this.localStore.dashboardData = result.dashboard
                  const newIFrame = document.getElementById("scaled-iframe").cloneNode()
                  newIFrame.src = localStore.dashboardData.configLink
                  document.getElementById("scaled-iframe").parentNode.replaceChild(newIFrame, document.getElementById("scaled-iframe"))
                  store.state.loading = false
                }
              }
          )
          .catch((error) => {
            console.log(error)
            store.state.loading=false
            }
          )
    }
  },

  computed:{
    somme: function(){
      //to update piechart when there are changes
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.updatePieChart++
      return localStore.dashboardData.withOlvidId+localStore.dashboardData.withoutOlvidId*localStore.dashboardData.withoutOlvidId
    },

    returnConfigLink:function(){
      return localStore.dashboardData.configLink
    }
  },

  beforeMount() {
    localStore.realmAsked = this.$route.params.realmName
    this.launchOlvidDashboardRequest()
  },

}
</script>

<style scoped>

#scaled-iframe {
  zoom: 0.5;
  -moz-transform: scale(0.5);
  -moz-transform-origin: 0 0;
  -o-transform: scale(0.5);
  -o-transform-origin: 0 0;
  -webkit-transform: scale(0.5);
  -webkit-transform-origin: 0 0;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  #scaled-iframe {
    zoom: 1;
  }
}

#dashboard-flow {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: stretch;
  position: relative;
  gap: 50px;

}

#configlinkcontainer{
  max-width: 280px;
  text-align: center;
}
#nbuserscontainer{
  max-width: 405px;
  text-align: center;
}

#dashboard-flow > * {
  position: relative;
  flex-shrink: 1;
  flex-grow: 1;
  flex-basis: 300px;
}

#piechart {
  width: 400px;
}

#scaled-iframe {
  width: 200%;
  max-width: 550px ;
  height: 200%;
  border: none;
}

#wrap {
  width: 100%;
  height: 600px;
  overflow: hidden;
}
iframe {
  pointer-events: none;
}

@media (max-width: 400px) {
  #piechart {
    width: 275px;
  }
  #nbuserscontainer{
    max-width: 280px;
  }
}

</style>