import {store} from "../main";
export const api = {

    genericRequest(value){
        return new Promise(function(resolve, reject) {
            //Request
            try {
                const xhr = new XMLHttpRequest();
                xhr.open('POST', '/auth/realms/' + store.state.authenticationInfo.redirectRealm + '/olvid-rest/configuration');
                xhr.onreadystatechange = function () {
                    try {
                        if (xhr.readyState === 4) {
                            const obj = JSON.parse(xhr.responseText)
                            if (obj !== undefined) {
                                if (obj.error === 2) {
                                    window.location.href = "/auth/realms/" + store.state.authenticationInfo.redirectRealm + "/account/#/olvid-console"
                                    store.state.badRealmType = false
                                    store.state.permissionDenied = false
                                    reject(obj.error)
                                } else if (obj.error === 5) {
                                    store.state.badRealmType = true
                                    reject(obj.error)
                                } else if (obj.queryNotAllowed !== undefined && obj.queryNotAllowed) {
                                    store.state.badRealmType = false
                                    store.state.permissionDenied = true
                                    reject("query not allowed")
                                } else {
                                    store.state.badRealmType = false
                                    store.state.permissionDenied = false
                                    resolve(obj)
                                }
                            } else {
                                reject("Unparsable response")
                            }
                        }
                    } catch (error) {
                        reject(error)
                    }
                }
                //Fail
                xhr.onerror = function (e) {
                    reject(e)
                }
                xhr.send(JSON.stringify(value))
            } catch (error) {
                reject(error)
            }

        })

    },


    getRealmsConfigurationRequest() {
        //3.QUERY_GET_REALMS
        return(this.genericRequest({q:3}))
    },

    getRealmsMainAttributesRequest(){
        //4.QUERY_GET_REALMS_MAIN_ATTRIBUTES
        return(this.genericRequest({q:4}))
    },

    setRealmsConfigurationRequest(realmListJsonString) {
        //6.QUERY_SET_REALMS_CONFIG
        return(this.genericRequest({q:6,realmListSettings:JSON.parse(realmListJsonString)}))
    },

    setAdminsRoleRequest(realmId,roleList) {
        //7.QUERY_SET_ROLE
        return this.genericRequest({q:7,realmId:realmId,roleList:roleList})
    },

    getAdminsListRequest(realmId) {
        //8.QUERY_GET_ADMINS
        return this.genericRequest({q:8,realmId:realmId})
    },

    getUsersListRequest(realmId, filters, page) {
        //9.QUERY_GET_USERS_FILTERED
        const offset = (page - 1) * store.state.limit
        return this.genericRequest({q:9,realmId:realmId,filters:filters,limit:store.state.limit,offset:offset})
    },

    getDashboardDataRequest(realmId) {
        //10.QUERY_GET_DASHBOARD
        return this.genericRequest({q:10,realmId:realmId})
    },

    revokeOneUserRequest(realmId,id,revocationType){
        //11.QUERY_REVOKE_USER
        return this.genericRequest({q:11,realmId:realmId,id:id,revocationType:revocationType})
    },

    getRevocationsListRequest(realmId){
        //12.QUERY_GET_REVOCATION
        return this.genericRequest({q:12,realmId:realmId})
    },

    signOutUserRequest(realmId, id) {
        //13.QUERY_SIGNOUT_USER
        return this.genericRequest({q:13,realmId:realmId,id:id})
    },

    wipeUserDataOnOlvidRevocationDatabaseRequest(realmId,revocationId){
        //14.QUERY_DELETE_USER_REVOKED_DATA
        return this.genericRequest({q:14,realmId:realmId,revocationId})
    },

    getLogsRequest(fromTimestamp, toTimestamp) {
        //15.QUERY_GET_REALMS
        return(this.genericRequest({q:15, fromTimestamp: fromTimestamp, toTimestamp: toTimestamp}))
    },

    createAdminUser(adminUserJSonString) {
        //16.QUERY_ADD_ADMIN
        return(this.genericRequest({q:16, adminUser: JSON.parse(adminUserJSonString)}))
    },

    removeAdmins(adminIdListJSonString) {
        //17.QUERY_REMOVE_ADMIN
        return(this.genericRequest({q:17, adminIdList: JSON.parse(adminIdListJSonString)}))
    },

    sendPasswordEmail(password, username) {
        //18.QUERY_SEND_AUTOMATIC_PASSWORD_EMAIL
        return(this.genericRequest({q:18, password: password, username: username}))
    },

    authenticationVerification:function(realmId = "master"){
        return new Promise(function(resolve, reject){
            //Request
            try{
                const xhr=new XMLHttpRequest();
                xhr.open('POST', '/auth/realms/' + realmId + '/olvid-rest/authenticationDiscovery');
                //Success
                xhr.onreadystatechange = function() {
                    try {
                        if (xhr.readyState === 4) {
                            //Add realms in store
                            const obj = JSON.parse(xhr.responseText)
                            if (obj === undefined){
                                reject(store.state.dictionary.rejectMessageAuthentificationVerification)
                            }
                            resolve(obj)
                        }
                    }
                    catch (error){
                        reject(error)
                    }
                }
                //Fail
                xhr.onerror = function (e) {
                    reject(e)
                }
                xhr.send(JSON.stringify({q:1}))
            }
            catch (error) {
                reject(error)
            }

        })
    },

    signOut:function (){
        return new Promise(function(resolve, reject){
            //Request
            try{
                const xhr=new XMLHttpRequest();
                xhr.open('POST', '/auth/realms/' + store.state.authenticationInfo.redirectRealm + '/olvid-rest/authenticationDiscovery');
                //Success
                xhr.onreadystatechange = function() {
                    if (xhr.readyState === 4) {
                        try{
                            const obj = JSON.parse(xhr.responseText)
                            if (obj === undefined){
                                reject(store.state.dictionary.rejectMessageAuthentificationVerification)
                            }
                            if (store.state.authenticationInfo.redirectRealm === "master") {
                                window.location.href = "/auth/admin/" + store.state.authenticationInfo.redirectRealm + "/console/"
                            }
                            else  {
                                window.location.href = "/auth/realms/" + store.state.authenticationInfo.redirectRealm + "/account/#/olvid-console"
                            }
                            resolve(obj)
                        }
                        catch (error) {
                            reject(error)
                        }
                    }
                }
                //Fail
                xhr.onerror = function (e) {
                    reject(e)
                }
                xhr.send(JSON.stringify({q:2}))
            }
            catch (error) {
                reject(error)
            }

        })
    }

}