<template>
<div>
  <b-dropdown
      :disabled="dropdownDisabled"
      size="sm"
      :text="dropdownLabel"
      class="m-md-2"
      :variant="dropdownVariant"
      right
      v-b-tooltip.hover.v-light="{
                  title:storeState.dictionary.tooltipCantChangeYourRole,
                  disabled:!dropdownDisabled
                }"
  >
    <span v-for="role in storeState.dictionary.roleArray" :key="role">
      <b-dropdown-item @click="onDropdownItemClickFunction(username,role)">
        {{ role }}
        <font-awesome-icon
            v-show="userRole===role"
            icon="check"
            class="checkicon"
        />
      </b-dropdown-item>
      <b-dropdown-divider v-show="storeState.dictionary.roleArray.indexOf(role)!==(storeState.dictionary.roleArray.length-1)"></b-dropdown-divider>
    </span>

  </b-dropdown>
</div>
</template>

<script>
import {store} from "../main";

export default {
  name: "DropdownAdminsListPage",
  props:{
    dropdownDisabled:Boolean,
    dropdownLabel:String,
    dropdownVariant:String,
    userRole:String,
    username:String,
    onDropdownItemClickFunction:Function,
  },
  data:function (){
    return{
      storeState:store.state,
    }
  }

}
</script>

<style scoped>
.checkicon{
  color: #2f65f5;
  margin-left:25px
}
</style>