import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import './plugins/bootstrap-vue'
import App from './App.vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faSync } from "@fortawesome/free-solid-svg-icons";
import { faBars} from "@fortawesome/free-solid-svg-icons";
import { faLock } from '@fortawesome/free-solid-svg-icons'
import { faLink } from "@fortawesome/free-solid-svg-icons";
import { faCheck} from "@fortawesome/free-solid-svg-icons";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons";
import { faCopy } from "@fortawesome/free-solid-svg-icons";
import { faQuestionCircle} from "@fortawesome/free-solid-svg-icons";
import { faSignOutAlt} from "@fortawesome/free-solid-svg-icons";
import { faUserCircle} from "@fortawesome/free-solid-svg-icons";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import VueMeta from 'vue-meta';
import router from "./router"
import * as VueChartJs from "vue-chartjs";
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import {dictionary} from "./dictionary/dictionary";
import Router from 'vue-router';

library.add(faTrash,faLock,faSync,faBars,faLink,faCheck,faUser,faEnvelope,faArrowRight,faCopy,faSignOutAlt,faUserCircle,faQuestionCircle)

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(VueMeta);
Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)

//avoid DuplicatedNavigation exception
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
};

Vue.use(Router);
Vue.config.productionTip = false

export const store = {
  state: {
      dictionary : dictionary,
      enablePrometheusMetrics: false,
      limit:100, // Is it a global or a userList component's variable ?
      loading:false, //to show spinner
      authenticationInfo:{
        realm:"",
        username:"",
        email:"",
        redirectRealm:"",
        },
      realmMainData:{},
      authentication: false,
      screenWidth: window.innerWidth,
      badRealmType: false,
      permissionDenied: false,
      dontLeave: false,
  },
}

Vue.component('pie-chart', {
    extends: VueChartJs.Pie,
    props: ['withOlvidId','withoutOlvidId'],
    data: function () {
        return {
            datacollection: {
                labels: [store.state.dictionary.piechartLabel.withOlvidId,store.state.dictionary.piechartLabel.withoutOlvidId],
                datasets: [
                    {
                        backgroundColor: [ '#2f65f5', '#00bcd4' ],
                        data: [this.withOlvidId,this.withoutOlvidId],
                    },
                ],
            },
            options: {
                responsive: true,
                legend: {
                    display: true,
                    position: 'right',
                },
            },

        }
    },
    mounted () {
        // this.chartData is created in the mixin
        this.renderChart(this.datacollection, this.options)
    }
})
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
